/* src/pages/LogsPage.css - Seguindo padro do sistema */

.page-container {
    min-height: 100vh;
    background: #1a1a1a !important;
    background-attachment: fixed !important;
}

/* =======================================================
   RESPONSIVIDADE GERAL E NOTEBOOKS
   ======================================================= */

.container {
    width: 90%;         /* Desktop padro */
    margin: 0 auto;
    padding: 20px;
}

/* Responsividade especfica para notebooks (1024px - 1366px) */
@media (min-width: 1024px) and (max-width: 1366px) {
    .container {
        width: 95% !important;  /* Expandir para notebooks */
        padding: 15px 20px;     /* Ajustar padding */
    }

    .table th,
    .table td {
        padding: 10px 12px;     /* Otimizar espaamento */
        font-size: 0.9rem;      /* Ajustar fonte */
    }

    .table {
        min-width: 1200px;      /* Garantir espao para todas colunas de logs */
    }
}

/* =======================================================
   CABEALHO DA PGINA - TEMA ESCURO
   ======================================================= */

.logs-header {
    background: #2d2d2d !important;         /* Fundo escuro */
    border-radius: 15px;
    padding: 25px;
    margin-bottom: 25px;
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
    border: 1px solid #444;                  /* Borda escura */
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.logs-title h1 {
    font-size: 1.8rem;
    font-weight: 700;
    color: #f0f0f0 !important;              /* Texto claro */
    margin: 0 0 8px 0;
    display: flex;
    align-items: center;
    gap: 10px;
}

.logs-icon {
    color: #3498db;
}

.eventos-criticos h3 {
    display: flex;
    align-items: center;
    gap: 8px;
}

.eventos-criticos-icon {
    color: #e74c3c;
}

.logs-title p {
    color: #7f8c8d;
    margin: 0;
    font-size: 1rem;
}

.logs-actions {
    display: flex;
    gap: 12px;
    margin-left: auto;
}

.btn-filter,
.btn-refresh {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 12px 20px;
    border: none;
    border-radius: 10px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 0.95rem;
}

.btn-filter {
    background: linear-gradient(135deg, #6c757d, #495057);
    color: white;
}

.btn-filter:hover {
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(108, 117, 125, 0.3);
}

.btn-filter.active {
    background: linear-gradient(135deg, #2d7a31, #45a049);
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(30, 90, 35, 0.3);
}

.btn-refresh {
    background: linear-gradient(135deg, #3498db, #2980b9);
    color: white;
}

.btn-refresh:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(52, 152, 219, 0.3);
}

.btn-refresh:disabled {
    opacity: 0.6;
    cursor: not-allowed;
    transform: none;
}

/* =======================================================
   EVENTOS CRTICOS
   ======================================================= */

.eventos-criticos {
    background: rgba(231, 76, 60, 0.1);
    border: 1px solid rgba(231, 76, 60, 0.3);
    border-radius: 15px;
    padding: 20px;
    margin-bottom: 25px;
    backdrop-filter: blur(10px);
}

.eventos-criticos h3 {
    color: #e74c3c;
    font-size: 1.3rem;
    font-weight: 700;
    margin: 0 0 16px 0;
}

.eventos-criticos-lista {
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.evento-critico-item {
    background: rgba(255, 255, 255, 0.95);
    border: 1px solid rgba(231, 76, 60, 0.2);
    border-radius: 12px;
    padding: 15px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: all 0.3s ease;
}

.evento-critico-item:hover {
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(231, 76, 60, 0.1);
}

.evento-info {
    display: flex;
    gap: 12px;
    align-items: center;
}

.evento-tipo {
    font-weight: 600;
    color: #e74c3c;
    font-size: 0.95rem;
}

.evento-data {
    color: #7f8c8d;
    font-size: 0.85rem;
}

.evento-descricao {
    color: #2c3e50;
    font-size: 0.9rem;
}

/* =======================================================
   PAINEL DE FILTROS
   ======================================================= */

.filtros-panel {
    background: rgba(255, 255, 255, 0.95);
    border-radius: 15px;
    padding: 25px;
    margin-bottom: 25px;
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.2);
}

.filtros-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
    margin-bottom: 25px;
}

.filtro-item {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.filtro-item label {
    font-weight: 600;
    color: #2c3e50;
    font-size: 0.9rem;
}

.filtro-item select,
.filtro-item input {
    padding: 12px 15px;
    border: 2px solid #e9ecef;
    border-radius: 10px;
    font-size: 0.95rem;
    background: #2d2d2d !important;          /* Fundo escuro */
    color: #f0f0f0 !important;               /* Texto claro */
    border: 1px solid #444;                  /* Borda escura */
    transition: all 0.3s ease;
}

.filtro-item select:focus,
.filtro-item input:focus {
    outline: none;
    border-color: #2d7a31;
    box-shadow: 0 0 0 3px rgba(30, 90, 35, 0.1);
}

.filtros-actions {
    display: flex;
    gap: 15px;
    padding-top: 20px;
    border-top: 2px solid #e9ecef;
}

.btn-apply {
    background: linear-gradient(135deg, #2d7a31, #45a049);
    color: white;
    border: none;
    padding: 12px 25px;
    border-radius: 10px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
}

.btn-apply:hover {
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(30, 90, 35, 0.3);
}

.btn-clear {
    background: linear-gradient(135deg, #6c757d, #495057);
    color: white;
    border: none;
    padding: 12px 25px;
    border-radius: 10px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
}

.btn-clear:hover {
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(108, 117, 125, 0.3);
}

/* =======================================================
   CONTAINER DA TABELA
   ======================================================= */

.logs-table-container {
    background: #2d2d2d !important;              /* Fundo escuro da tabela */
    border-radius: 15px;
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(10px);
    border: 1px solid #444;                      /* Borda escura */
    overflow: hidden;
}

/* =======================================================
   ESTADOS DE LOADING E VAZIO
   ======================================================= */

.loading-state,
.empty-state {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 60px 20px;
    color: #7f8c8d;
}

.loading-state svg,
.empty-state svg {
    color: #bdc3c7;
    margin-bottom: 16px;
}

.loading-state h3,
.empty-state h3 {
    font-size: 1.3rem;
    font-weight: 600;
    margin: 0 0 8px 0;
    color: #2c3e50;
}

.loading-state p,
.empty-state p {
    margin: 0;
    font-size: 1rem;
}

/* =======================================================
   TABELA DE LOGS
   ======================================================= */

.logs-table {
    width: 100%;
    border-collapse: collapse;
}

.logs-table thead {
    background: #1a1a1a !important;             /* Fundo escuro do cabealho */
    border-bottom: 2px solid #444;              /* Borda escura */
}

.logs-table th {
    background: #1a1a1a !important;             /* Fundo escuro do cabealho */
    border-bottom: 2px solid #444;              /* Borda escura */
    color: #f0f0f0 !important;                  /* Texto claro */
    font-size: 0.8rem;
    font-weight: 700;
    letter-spacing: 0.5px;
    padding: 15px 12px;
    position: -webkit-sticky;
    position: sticky;
    text-transform: uppercase;
    top: 0;
    z-index: 10;
}

.logs-table tbody tr {
    border-bottom: 1px solid #444;              /* Borda escura */
    transition: all 0.3s ease;
    background: #2d2d2d !important;             /* Fundo escuro das linhas */
}

.logs-table tbody tr:nth-child(even) {
    background-color: #333 !important;          /* Fundo escuro alternado */
}

.logs-table tbody tr:nth-child(even):hover {
    background-color: #3d3d3d !important;      /* Hover escuro alternado */
}

.logs-table tbody tr:hover {
    background: #3d3d3d !important;             /* Hover escuro */
}

.logs-table tbody tr.log-critico {
    background: rgba(231, 76, 60, 0.05);
    border-left: 4px solid #e74c3c;
}

.logs-table tbody tr.log-critico:hover {
    background: rgba(231, 76, 60, 0.1);
}

.logs-table td {
    border-bottom: 1px solid #444;              /* Borda escura */
    padding: 12px;
    font-size: 0.9rem;
    vertical-align: middle;
    color: #f0f0f0 !important;                  /* Texto claro */
}

/* =======================================================
   CLULAS ESPECFICAS
   ======================================================= */

.log-data {
    color: #7f8c8d;
    white-space: nowrap;
    min-width: 160px;
    font-family: 'Courier New', monospace;
    font-size: 0.85rem;
}

.usuario-info {
    display: flex;
    align-items: center;
    gap: 8px;
}

.usuario-info svg {
    color: #95a5a6;
}

.log-modulo,
.log-acao {
    white-space: nowrap;
}

.log-descricao {
    max-width: 300px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: #f0f0f0 !important;               /* Texto claro para tema escuro */
}

.log-ip {
    color: #7f8c8d;
    font-family: 'Courier New', monospace;
    font-size: 0.8rem;
}

/* =======================================================
   BADGES
   ======================================================= */

.badge {
    display: inline-flex;
    align-items: center;
    padding: 6px 12px;
    border-radius: 8px;
    font-size: 0.8rem;
    font-weight: 600;
    white-space: nowrap;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.badge-modulo {
    background: linear-gradient(135deg, #8e44ad, #9b59b6);
    color: white;
}

.badge-acao {
    background: #ecf0f1;
    color: #2c3e50;
}

/* Cores dos badges de ao */
.badge-acao.bg-green-100 {
    background: linear-gradient(135deg, #27ae60, #2ecc71);
    color: white;
}
.badge-acao.bg-blue-100 {
    background: linear-gradient(135deg, #3498db, #2980b9);
    color: white;
}
.badge-acao.bg-red-100 {
    background: linear-gradient(135deg, #e74c3c, #c0392b);
    color: white;
}
.badge-acao.bg-purple-100 {
    background: linear-gradient(135deg, #8e44ad, #9b59b6);
    color: white;
}
.badge-acao.bg-gray-100 {
    background: linear-gradient(135deg, #95a5a6, #7f8c8d);
    color: white;
}
.badge-acao.bg-indigo-100 {
    background: linear-gradient(135deg, #3f51b5, #5c6bc0);
    color: white;
}
.badge-acao.bg-yellow-100 {
    background: linear-gradient(135deg, #f39c12, #e67e22);
    color: white;
}

/* =======================================================
   PRIORIDADE
   ======================================================= */

.prioridade {
    font-size: 0.8rem;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.prioridade.critico {
    color: #e74c3c;
}

.prioridade.normal {
    color: #27ae60;
}

/* =======================================================
   BOTO DE AES
   ======================================================= */

.btn-details {
    background: linear-gradient(135deg, #95a5a6, #7f8c8d);
    border: none;
    padding: 8px;
    border-radius: 8px;
    cursor: pointer;
    color: white;
    transition: all 0.3s ease;
}

.btn-details:hover {
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(149, 165, 166, 0.3);
}

/* =======================================================
   PAGINAO
   ======================================================= */

.pagination {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 25px;
    border-top: 2px solid #444;                 /* Borda escura */
    background: #1a1a1a !important;             /* Fundo escuro da paginao */
}

.pagination-info {
    color: #7f8c8d;
    font-size: 0.9rem;
    font-weight: 500;
}

.pagination-controls {
    display: flex;
    align-items: center;
    gap: 10px;
}

.btn-pagination {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 10px 15px;
    border: none;
    background: linear-gradient(135deg, #95a5a6, #7f8c8d);
    border-radius: 8px;
    cursor: pointer;
    font-size: 0.9rem;
    color: white;
    font-weight: 500;
    transition: all 0.3s ease;
}

.btn-pagination:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(149, 165, 166, 0.3);
}

.btn-pagination:disabled {
    opacity: 0.5;
    cursor: not-allowed;
    transform: none;
    box-shadow: none;
}

.page-numbers {
    display: flex;
    gap: 5px;
}

.btn-page {
    padding: 10px 15px;
    border: 1px solid #444;
    background: #2d2d2d !important;          /* Fundo escuro */
    color: #f0f0f0 !important;               /* Texto claro */
    border-radius: 8px;
    cursor: pointer;
    font-size: 0.9rem;
    color: #2c3e50;
    font-weight: 500;
    transition: all 0.3s ease;
    min-width: 40px;
    border: 2px solid #e9ecef;
}

.btn-page:hover {
    background: #444 !important;                /* Hover escuro */
    border-color: #2d7a31;
    transform: translateY(-1px);
}

.btn-page.active {
    background: linear-gradient(135deg, #2d7a31, #45a049);
    color: white;
    border-color: #2d7a31;
    transform: translateY(-1px);
    box-shadow: 0 5px 15px rgba(30, 90, 35, 0.3);
}

/* =======================================================
   MODAL
   ======================================================= */

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    backdrop-filter: blur(5px);
}

.modal-content {
    background: linear-gradient(135deg, #1e293b 0%, #0f172a 100%);
    border-radius: 16px;
    max-width: 600px;
    width: 90%;
    max-height: 90vh;
    overflow: hidden;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
    border: 1px solid rgba(255, 255, 255, 0.1);
    display: flex;
    flex-direction: column;
}

.modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 24px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.modal-header h3 {
    font-size: 1.5rem;
    font-weight: 700;
    color: white;
    margin: 0;
    display: flex;
    align-items: center;
    gap: 10px;
}

.btn-close {
    background: rgba(255, 255, 255, 0.1);
    border: none;
    font-size: 24px;
    color: rgba(255, 255, 255, 0.7);
    cursor: pointer;
    width: 36px;
    height: 36px;
    border-radius: 8px;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: center;
}

.btn-close:hover {
    background: rgba(255, 255, 255, 0.2);
    color: white;
    transform: scale(1.1);
}

.modal-body {
    padding: 24px;
    overflow-y: auto;
    flex: 1;
    min-height: 0;
}

/* Scroll customizado para o body */
.modal-body::-webkit-scrollbar {
    width: 8px;
}

.modal-body::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.05);
    border-radius: 4px;
}

.modal-body::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.2);
    border-radius: 4px;
}

.modal-body::-webkit-scrollbar-thumb:hover {
    background: rgba(255, 255, 255, 0.3);
}

/* =======================================================
   DETALHES DO LOG
   ======================================================= */

.log-details {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.detail-row {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.detail-label {
    font-weight: 700;
    color: rgba(255, 255, 255, 0.9);
    font-size: 0.9rem;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.detail-value {
    color: rgba(255, 255, 255, 0.7);
    font-size: 1rem;
    word-break: break-word;
    background: rgba(255, 255, 255, 0.05);
    padding: 10px 15px;
    border-radius: 10px;
    border-left: 4px solid #3b82f6;
}

.detail-section {
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    padding-top: 20px;
}

.dados-contexto {
    background: rgba(0, 0, 0, 0.3);
    color: #ecf0f1;
    border-radius: 10px;
    padding: 20px;
    border: 1px solid rgba(255, 255, 255, 0.1);
    font-family: 'Courier New', monospace;
}

.contexto-item {
    display: flex;
    margin-bottom: 12px;
}

.contexto-item:last-child {
    margin-bottom: 0;
}

.contexto-chave {
    font-weight: 600;
    color: #3b82f6;
    min-width: 140px;
    font-size: 0.9rem;
}

.contexto-valor {
    color: rgba(255, 255, 255, 0.8);
    font-size: 0.9rem;
    word-break: break-word;
}

/* =======================================================
   ANIMAES
   ======================================================= */

.spinning {
    animation: spin 1s linear infinite;
}

@keyframes spin {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
}

/* =======================================================
   RESPONSIVIDADE
   ======================================================= */

@media (max-width: 768px) {
    .container {
        padding: 15px;
    }

    .logs-header {
        flex-direction: column;
        gap: 20px;
        text-align: center;
    }

    .logs-actions {
        width: 100%;
        justify-content: center;
        margin-left: 0;
    }

    .filtros-grid {
        grid-template-columns: 1fr;
    }

    .logs-table-container {
        overflow-x: auto;
    }

    .logs-table {
        min-width: 800px;
    }

    .pagination {
        flex-direction: column;
        gap: 20px;
        text-align: center;
    }

    .modal-content {
        width: 95%;
        margin: 20px;
    }

    .modal-header,
    .modal-body {
        padding: 20px;
    }
}