/* AssociadosPage.css - Estilos da pgina de associados */
/* Segue os mesmos padres de ValidacaoAssociadosPage.css e ControlePage.css */

/* Importar estilos padronizados de indicadores */
@import '../styles/indicators.css';

/* =======================================================
   PAGE CONTAINER - HERDA DO PADRO
   ======================================================= */

.page-container {
  min-height: 100vh;
  background: #1a1a1a !important;
  background-attachment: fixed !important;
}

.container {
  width: 90%;
  margin: 0 auto;
  padding: 20px;
}

/* Responsividade especfica para notebooks (1024px - 1366px) */
@media (min-width: 1024px) and (max-width: 1366px) {
  .container {
    width: 95% !important;
    padding: 15px 20px;
  }
}

/* =======================================================
   SEO DE FILTROS - PADRO DAS PGINAS
   ======================================================= */

.filters-section {
  background: white;
  border-radius: 15px;
  padding: 18px;
  margin-bottom: 18px;
  box-shadow: 0 5px 20px rgba(0,0,0,0.1);
  position: relative;
  z-index: 1;
}

.filters-container {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.filters-grid {
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 12px;
  align-items: end;
}

.filter-group {
  display: flex;
  flex-direction: column;
}

.filter-group label {
  font-weight: 600;
  margin-bottom: 8px;
  color: #333;
  font-size: 0.9rem;
}

.filter-group input,
.filter-group select {
  padding: 12px 15px;
  border: 2px solid #e1e1e1;
  border-radius: 8px;
  font-size: 0.95rem;
  transition: all 0.3s ease;
  background: white;
}

.filter-group input:focus,
.filter-group select:focus {
  outline: none;
  border-color: #2d7a31;
  box-shadow: 0 0 0 3px rgba(30, 90, 35, 0.1);
}

.filter-busca {
  flex: 1;
}

.filter-actions {
  display: flex;
  gap: 10px;
  align-items: center;
}

/* =======================================================
   TABELA - FORMATAO PADRO DARK MODE
   ======================================================= */

.table-section {
  background: white;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
  margin-top: 30px;
}

.table-wrapper {
  overflow-x: auto;
  max-height: 70vh;
  position: relative;
  background: #2d2d2d !important;
}

/* Tabela - Dark Mode */
.table {
  width: 100% !important;
  border-collapse: collapse !important;
  font-size: 0.85rem !important;
  background: #2d2d2d !important;
  min-width: 800px !important;
}

/* Cabealho da tabela */
.table thead {
  position: sticky !important;
  top: 0 !important;
  background: #333 !important;
  z-index: 10 !important;
}

.table th {
  background: #333 !important;
  padding: 15px 12px !important;
  text-align: left !important;
  font-weight: 600 !important;
  color: #f0f0f0 !important;
  border-bottom: 2px solid #555 !important;
  font-size: 0.85rem !important;
  white-space: nowrap !important;
  min-width: 100px !important;
}

.table th:hover {
  background: #404040 !important;
}

/* Clulas da tabela */
.table td {
  padding: 12px !important;
  border-bottom: 1px solid #444 !important;
  font-size: 0.85rem !important;
  vertical-align: middle !important;
  min-width: 80px !important;
  color: #f0f0f0 !important;
  background: #2d2d2d !important;
}

/* Linhas - sem zebra, hover uniforme */
.table tbody tr {
  background: #2d2d2d !important;
}

.table tbody tr:hover {
  background: #3d3d3d !important;
}

/* =======================================================
   ELEMENTOS ESPECFICOS DA TABELA
   ======================================================= */

.numero-proposta {
  font-family: monospace;
  background: #3d3d3d !important;
  color: #f0f0f0 !important;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 0.9rem;
  border: 1px solid #555;
}

.cliente-info {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.nome-cliente {
  font-weight: 600;
  color: #f0f0f0 !important;
}

.contato {
  color: #ccc !important;
  font-size: 0.85rem;
}

.contato.email {
  max-width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: inline-block;
}

.badge-uc {
  background: linear-gradient(135deg, #2d7a31, #1e5a23) !important;
  color: white !important;
  padding: 4px 10px;
  border-radius: 10px;
  font-size: 0.8rem;
  font-weight: 600;
  display: inline-block;
}

.acoes-cell {
  display: flex;
  gap: 8px;
}

/* =======================================================
   PAGINAO
   ======================================================= */

.pagination-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  background: #333;
  border-top: 1px solid #444;
}

.pagination-info {
  color: #ccc;
  font-size: 0.85rem;
}

.pagination-controls {
  display: flex;
  align-items: center;
  gap: 10px;
}

.pagination-current {
  color: #f0f0f0;
  font-size: 0.9rem;
  padding: 0 10px;
}

/* =======================================================
   ESTADOS (Loading, Empty)
   ======================================================= */

.loading-container,
.empty-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px 20px;
  background: #2d2d2d;
  color: #f0f0f0;
}

.loading-container svg,
.empty-container svg {
  margin-bottom: 15px;
  opacity: 0.5;
  color: #888;
}

.empty-container h3 {
  margin: 0 0 10px 0;
  color: #f0f0f0;
}

.empty-container p {
  color: #888;
  margin: 0;
}

.spinning {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

/* =======================================================
   QUICK STATS - ESTATSTICAS RPIDAS
   ======================================================= */

.quick-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
  margin-bottom: 20px;
}

.stat-card {
  background: linear-gradient(135deg, #2d7a31, #1e5a23);
  border-radius: 12px;
  padding: 18px;
  display: flex;
  align-items: center;
  gap: 15px;
  box-shadow: 0 5px 20px rgba(0,0,0,0.15);
}

.stat-icon {
  width: 50px;
  height: 50px;
  background: rgba(255,255,255,0.15);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.stat-content {
  display: flex;
  flex-direction: column;
}

.stat-label {
  font-size: 0.8rem;
  color: rgba(255,255,255,0.8);
  margin-bottom: 3px;
}

.stat-value {
  font-size: 1.5rem;
  font-weight: 700;
  color: white;
}

.stat-value-small {
  font-size: 1rem;
  font-weight: 600;
  color: white;
}

/* =======================================================
   MODAL - UCs LISTA
   ======================================================= */

.empty-ucs {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
  color: #888;
}

.empty-ucs svg {
  margin-bottom: 10px;
  opacity: 0.5;
}

.ucs-lista {
  display: flex;
  flex-direction: column;
  gap: 12px;
  max-height: 400px;
  overflow-y: auto;
}

.uc-card {
  background: #3d3d3d;
  border: 1px solid #555;
  border-radius: 8px;
  padding: 15px;
}

.uc-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.uc-numero {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 600;
  color: #4ade80;
}

.status-badge {
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 0.75rem;
  font-weight: 600;
}

.status-badge.status-associado {
  background: #166534;
  color: #bbf7d0;
}

.status-badge.status-esteira {
  background: #854d0e;
  color: #fef08a;
}

.status-badge.status-em-andamento {
  background: #1e40af;
  color: #bfdbfe;
}

.uc-info-details p {
  margin: 4px 0;
  font-size: 0.85rem;
  color: #ccc;
}

.uc-info-details strong {
  color: #f0f0f0;
}

/* Tab badge */
.tab-badge {
  background: #2d7a31;
  color: white;
  padding: 2px 8px;
  border-radius: 10px;
  font-size: 0.75rem;
  margin-left: 6px;
}

/* =======================================================
   RESPONSIVO
   ======================================================= */

@media (max-width: 768px) {
  .filters-grid {
    grid-template-columns: 1fr;
  }

  .filter-actions {
    justify-content: flex-end;
  }

  .quick-stats {
    grid-template-columns: 1fr;
  }

  .pagination-bar {
    flex-direction: column;
    gap: 10px;
  }

  .table {
    font-size: 0.8rem !important;
  }

  .table th,
  .table td {
    padding: 10px 8px !important;
  }

  .contato.email {
    max-width: 120px;
  }
}

/* =======================================================
   MODAL DE UNIFICAO DE ASSOCIADOS
   ======================================================= */

.modal-unificacao {
  max-width: 550px;
}

.modal-header-warning {
  background: linear-gradient(135deg, #d97706, #b45309) !important;
}

.modal-header-warning .modal-title-with-icon {
  color: white !important;
}

.modal-header-warning .modal-title-with-icon svg {
  color: white !important;
}

.unificacao-aviso {
  display: flex;
  align-items: flex-start;
  gap: 15px;
  padding: 20px;
  background: #fef3c7;
  border: 1px solid #f59e0b;
  border-radius: 10px;
  margin-bottom: 20px;
}

.unificacao-aviso .aviso-icon {
  color: #d97706;
  flex-shrink: 0;
}

.unificacao-aviso p {
  margin: 0;
  color: #92400e !important;
  line-height: 1.5;
}

.unificacao-aviso p strong {
  color: #78350f !important;
}

.unificacao-detalhes {
  margin-bottom: 20px;
}

.unificacao-detalhes h4 {
  margin: 0 0 12px 0;
  color: #f0f0f0;
  font-size: 0.95rem;
}

.associado-card {
  background: #3d3d3d;
  border: 1px solid #555;
  border-radius: 10px;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 15px;
}

.associado-info p {
  margin: 4px 0;
  color: #ccc;
  font-size: 0.9rem;
}

.associado-info strong {
  color: #f0f0f0;
}

.associado-stats {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.stat-item {
  display: flex;
  align-items: center;
  gap: 6px;
  background: #2d2d2d;
  padding: 6px 12px;
  border-radius: 6px;
  color: #4ade80;
  font-size: 0.85rem;
  font-weight: 600;
}

.stat-item svg {
  color: #4ade80;
}

.unificacao-acao {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 15px;
  background: #2d2d2d;
  border-radius: 10px;
  border-left: 4px solid #f59e0b;
}

.unificacao-acao .arrow-icon {
  color: #f59e0b;
  flex-shrink: 0;
}

.unificacao-acao p {
  margin: 0;
  color: #ccc;
  font-size: 0.9rem;
  line-height: 1.5;
}

.unificacao-acao strong {
  color: #fbbf24;
}

/* Boto warning */
.btn-warning {
  background: linear-gradient(135deg, #d97706, #b45309) !important;
  color: white !important;
  border: none !important;
}

.btn-warning:hover {
  background: linear-gradient(135deg, #b45309, #92400e) !important;
}

.btn-warning:disabled {
  background: #6b7280 !important;
  cursor: not-allowed;
}
