/* src/pages/UGsPage.css - LARGURA CORRIGIDA */

/* Importar estilos padronizados de indicadores */
@import '../styles/indicators.css';

.page-container {
  min-height: 100vh;
  background: #1a1a1a !important; /*  MUDAR ESTA LINHA */
  background-attachment: fixed !important;
}

/* =======================================================
   RESPONSIVIDADE GERAL E NOTEBOOKS
   ======================================================= */

.container {
  width: 90%;         /* Desktop padro */
  margin: 0 auto;
  padding: 20px;
}

/* Responsividade especfica para notebooks (1024px - 1366px) */
@media (min-width: 1024px) and (max-width: 1366px) {
  .container {
    width: 95% !important;  /* Expandir para notebooks */
    padding: 15px 20px;     /* Ajustar padding */
  }

  .table th,
  .table td {
    padding: 10px 12px;     /* Otimizar espaamento */
    font-size: 0.9rem;      /* Ajustar fonte */
  }

  .table {
    min-width: 1200px;      /* Garantir espao para todas colunas */
  }
}

/* =======================================================
   ESTATSTICAS RPIDAS - ESTILOS ESPECFICOS DA PGINA
   Os estilos base esto em ../styles/indicators.css
   ======================================================= */

/* Nenhum override necessrio - usar padro */

/* =======================================================
   SEO DE FILTROS
   ======================================================= */

.filters-section {
  background: white;
  border-radius: 15px;
  padding: 18px;
  margin-bottom: 18px;
  box-shadow: 0 5px 20px rgba(0,0,0,0.1);
}

.filters-container {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.filters-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 12px;
  align-items: end;
}

/* Layout horizontal para UGs - filtro e botes na mesma linha */
.filters-actions-row {
  display: flex;
  gap: 12px;
  align-items: end;
}

.filter-busca-ugs {
  flex: 1;
  min-width: 300px;
}

.actions-container-inline {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  align-items: center;
}

.filter-group {
  display: flex;
  flex-direction: column;
}

.filter-group label {
  font-weight: 600;
  margin-bottom: 8px;
  color: #333;
  font-size: 0.9rem;
}

.filter-group input,
.filter-group select {
  padding: 12px 15px;
  border: 2px solid #e1e1e1;
  border-radius: 8px;
  font-size: 0.95rem;
  transition: all 0.3s ease;
  background: white;
}

.filter-group input:focus,
.filter-group select:focus {
  outline: none;
  border-color: #2d7a31;
  box-shadow: 0 0 0 3px rgba(30, 90, 35, 0.1);
}

.actions-container {
  display: flex;
  gap: 15px;
  justify-content: flex-end;
  align-items: center;
  flex-wrap: wrap;
}

/* =======================================================
   SEO DE DADOS
   ======================================================= */

.data-section {
  background: white;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 5px 20px rgba(0,0,0,0.1);
}

/* =======================================================
   CONTAINER COM SCROLL HORIZONTAL PARA TABELA DE UGS
   ======================================================= */

.table-wrapper {
  overflow-x: auto;
  background: white;
  border-radius: 12px;
  box-shadow: 0 5px 20px rgba(0,0,0,0.1);
  position: relative;
}

/* Scroll customizado - usar padro global (tables-global.css) */

.table-header {
  padding: 20px 25px;
  background: #2d2d2d;
  border-bottom: 1px solid #e1e1e1;
  border-radius: 12px 12px 0 0;
}

.table-header h2 {
  margin: 0;
  color: #f0f0f0 !important;  /*  ALTERAR DE #333 para #f0f0f0 */
  font-size: 1.3rem;           /*  ALTERAR DE 1.2rem para 1.3rem */
  font-weight: 600;
  display: flex;               /*  ADICIONAR */
  align-items: center;         /*  ADICIONAR */
  gap: 10px;                   /*  ADICIONAR */
}

.table {
  width: 100%;
  border-collapse: collapse;
  min-width: 1000px;
}

.table th,
.table td {
  padding: 12px 15px;
  text-align: left;
  border-bottom: 1px solid #e1e1e1;
}

.table th {
  background: #f8f9fa;
  font-weight: 600;
  color: #333;
  position: sticky;
  top: 0;
  z-index: 2;
}

.table tr:hover {
  background: #f8f9fa;
}

/* Clulas especficas */
.usina-info .nome-usina {
  font-weight: 600;
}

.potencia-valor,
.capacidade-valor {
  font-family: monospace;
  font-weight: 600;
  color: #007bff;
}

.fator-capacidade {
  font-weight: 600;
  color: #28a745;
}

.ucs-count {
  font-weight: 600;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 0.9rem;
}

.ucs-count.has-ucs {
  background: rgba(40, 167, 69, 0.1);
  color: #28a745;
}

.ucs-count.no-ucs {
  background: rgba(108, 117, 125, 0.1);
  color: #6c757d;
}

.media-total {
  font-family: monospace;
  font-weight: 600;
  color: #17a2b8;
}

/* =======================================================
   AES DA TABELA - MOVIDO PARA CommonModalsPagesDark.css
   Estilos padronizados e unificados entre todas as pginas
   ======================================================= */

/* Os estilos dos botes de ao agora esto centralizados em:
   CommonModalsPagesDark.css - linha 367+

   Isso garante padronizao visual entre ProspecPage, ControlePage e UGsPage
   com botes de fundo neutro e apenas os cones coloridos.
*/

/* =======================================================
   ESTADOS DA PGINA
   ======================================================= */

.loading-state,
.empty-state {
  text-align: center;
  padding: 60px 20px;
  color: #666;
}

.loading-state .spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #f3f3f3;
  border-top: 4px solid #2d7a31;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 20px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.spinning {
  animation: spin 1s linear infinite;
}

.empty-icon {
  font-size: 4rem;
  margin-bottom: 20px;
  opacity: 0.5;
}

.empty-state h3 {
  margin-bottom: 10px;
  color: #495057;
}

.empty-state p {
  margin-bottom: 20px;
}

/* =======================================================
   BOTES
   ======================================================= */

.btn {
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.btn-primary {
  background: #007bff;
  color: white !important;
}

.btn-primary:hover {
  background: #0056b3;
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(0,123,255,0.3);
}

.btn-secondary {
  background: #6c757d;
  color: white;
}

.btn-secondary:hover {
  background: #545b62;
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(108,117,125,0.3);
}

/* =======================================================
   MODAL - DARK MODE SEGUINDO PADRO CommonModal.css
   ======================================================= */

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 20px;
}

/* Modal de UG com dark mode */
.modal-ug {
  background: linear-gradient(135deg, #1e293b 0%, #0f172a 100%) !important;
  border-radius: 16px !important;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5) !important;
  max-width: 600px !important;
  width: 90% !important;
}

/* Header do modal de UG - Dark Mode */
.modal-header-ug {
  background: rgba(255, 255, 255, 0.03) !important;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1) !important;
  padding: 24px !important;
  border-radius: 16px 16px 0 0 !important;
  display: flex !important;
  align-items: center !important;
  justify-content: space-between !important;
}

.modal-header-ug h2 {
  margin: 0 !important;
  color: white !important;
  font-size: 1.5rem !important;
  font-weight: 700 !important;
  display: flex !important;
  align-items: center !important;
  gap: 10px !important;
}

/* Body do modal de UG - Dark Mode */
.modal-body-ug {
  padding: 24px !important;
  background: transparent !important;
}

.modal-body-ug .form-grid {
  display: grid !important;
  grid-template-columns: 1fr 1fr !important;
  gap: 16px !important;
}

.modal-body-ug .form-group {
  display: flex !important;
  flex-direction: column !important;
  gap: 6px !important;
}

.modal-body-ug .form-group label {
  font-weight: 500 !important;
  color: rgba(255, 255, 255, 0.9) !important;
  font-size: 0.9rem !important;
  display: flex !important;
  align-items: center !important;
  gap: 8px !important;
}

.modal-body-ug .form-group input {
  padding: 12px 16px !important;
  border: 1px solid rgba(255, 255, 255, 0.1) !important;
  border-radius: 8px !important;
  font-size: 0.95rem !important;
  transition: all 0.3s ease !important;
  background: rgba(255, 255, 255, 0.05) !important;
  color: white !important;
}

.modal-body-ug .form-group input:focus {
  border-color: #3b82f6 !important;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1) !important;
  outline: none !important;
  background: rgba(255, 255, 255, 0.08) !important;
}

.modal-body-ug .form-group input:required:invalid {
  border-color: rgba(239, 68, 68, 0.5) !important;
}

.modal-body-ug .form-group input:required:valid {
  border-color: rgba(16, 185, 129, 0.5) !important;
}

/* Informaes da UG - Dark Mode */
.info-ug {
  margin: 20px 0 !important;
  padding: 16px !important;
  background: rgba(59, 130, 246, 0.1) !important;
  border-radius: 8px !important;
  border-left: 3px solid #3b82f6 !important;
}

.info-item {
  margin-bottom: 8px !important;
  font-size: 0.9rem !important;
  color: rgba(255, 255, 255, 0.7) !important;
}

.info-item:last-child {
  margin-bottom: 0 !important;
}

.info-item strong {
  color: rgba(255, 255, 255, 0.9) !important;
}

/* Footer do modal - Dark Mode */
.modal-ug .modal-footer {
  padding: 20px 24px !important;
  border-top: 1px solid rgba(255, 255, 255, 0.1) !important;
  background: rgba(255, 255, 255, 0.03) !important;
  border-radius: 0 0 16px 16px !important;
  display: flex !important;
  justify-content: flex-end !important;
  gap: 12px !important;
}

/* Boto fechar - Dark Mode */
.common-close-btn {
  background: transparent !important;
  border: none !important;
  color: rgba(255, 255, 255, 0.6) !important;
  cursor: pointer !important;
  padding: 8px !important;
  border-radius: 8px !important;
  transition: all 0.3s ease !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
}

.common-close-btn:hover {
  background: rgba(255, 255, 255, 0.1) !important;
  color: white !important;
}

/* Botes do modal - Dark Mode */
.modal-ug .btn {
  padding: 14px 20px !important;
  border: none !important;
  border-radius: 8px !important;
  font-size: 1rem !important;
  font-weight: 600 !important;
  cursor: pointer !important;
  transition: all 0.3s ease !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  gap: 8px !important;
}

.modal-ug .btn-primary {
  background: rgba(255, 255, 255, 0.1) !important;
  color: rgba(255, 255, 255, 0.9) !important;
  border: 1px solid rgba(255, 255, 255, 0.2) !important;
}

.modal-ug .btn-primary:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.15) !important;
  transform: translateY(-2px) !important;
  border-color: rgba(255, 255, 255, 0.3) !important;
}

.modal-ug .btn-secondary {
  background: rgba(255, 255, 255, 0.1) !important;
  color: rgba(255, 255, 255, 0.9) !important;
  border: 1px solid rgba(255, 255, 255, 0.2) !important;
}

.modal-ug .btn-secondary:hover {
  background: rgba(255, 255, 255, 0.15) !important;
  transform: translateY(-2px) !important;
}

.modal-ug .btn:disabled {
  opacity: 0.6 !important;
  cursor: not-allowed !important;
  transform: none !important;
}

/* Animao do modal de UG */
.modal-ug {
  animation: modalSlideIn 0.3s ease-out !important;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* =======================================================
   RESPONSIVIDADE
   ======================================================= */

@media (max-width: 1300px) {
  .container {
    max-width: 1100px;
    width: 88%;
  }
}

@media (max-width: 1200px) {
  .quick-stats {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .container {
    width: 95%;
    padding: 15px;
  }
  
  .quick-stats {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  
  .stat-card {
    padding: 15px;
  }
  
  .stat-value {
    font-size: 1.5rem;
  }
  
  .filters-section {
    padding: 20px 15px;
  }
  
  .filters-grid {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  
  .actions-container {
    justify-content: stretch;
  }
  
  .actions-container .btn {
    flex: 1;
  }
  
  .table-header {
    padding: 15px 20px;
  }
  
  .table-wrapper {
    overflow-x: auto !important;
    -webkit-overflow-scrolling: touch;     /* Scroll suave no iOS */
    border-radius: 0 0 12px 12px;
  }

  .table {
    min-width: 1000px !important;          /* Garantir espao para TODAS as colunas */
    font-size: 0.8rem;
  }

  .table th,
  .table td {
    padding: 8px 10px !important;          /* Padding otimizado */
    font-size: 0.75rem !important;         /* Fonte menor mas legvel */
    min-width: 80px;                       /* Largura mnima para cada coluna */
  }

  /* Colunas especficas com larguras otimizadas */
  .table th:nth-child(1), /* Nome da Usina */
  .table td:nth-child(1) {
    min-width: 150px !important;           /* Mais espao para o nome */
    max-width: 200px;
  }

  .table th:nth-child(9), /* Aes */
  .table td:nth-child(9) {
    min-width: 120px !important;           /* Espao para 3 botes */
    position: sticky;                      /* Manter aes visveis */
    right: 0;
    background: white;
    z-index: 1;
  }
  
  .modal-ug {
    width: 95% !important;
    max-width: none !important;
    margin: 10px !important;
  }

  .modal-body-ug .form-grid {
    grid-template-columns: 1fr !important;
    gap: 12px !important;
  }

  .modal-header-ug,
  .modal-body-ug,
  .modal-ug .modal-footer {
    padding: 16px !important;
  }

  /* Tabela responsiva - ocultar colunas desnecessrias em mobile */
  .table th:nth-child(2), /* Potncia CA */
  .table th:nth-child(3), /* Potncia CC */
  .table th:nth-child(4), /* Fator de Capacidade */
  .table th:nth-child(6), /* UCs Atribudas */
  .table td:nth-child(2), /* Potncia CA */
  .table td:nth-child(3), /* Potncia CC */
  .table td:nth-child(4), /* Fator de Capacidade */
  .table td:nth-child(6) { /* UCs Atribudas */
    display: none !important;
  }

  /* Ajustar largura das colunas restantes para UGs */
  .table th:nth-child(1), /* Nome da Usina */
  .table td:nth-child(1) {
    width: 40% !important;
  }

  .table th:nth-child(5), /* Capacidade */
  .table td:nth-child(5) {
    width: 30% !important;
  }

  .table th:nth-child(7), /* Mdia Total */
  .table td:nth-child(7) {
    width: 20% !important;
  }

  .table th:nth-child(8), /* Aes (se admin) */
  .table td:nth-child(8) {
    width: 10% !important;
  }
}

@media (max-width: 480px) {
  .container {
    width: 98%;
    padding: 10px;
  }
  
  .table th,
  .table td {
    padding: 6px 4px;
    font-size: 0.75rem;
  }

  /* Estilos responsivos de .action-btn movidos para CommonModalsPagesDark.css */
}

/* ===================================================
   INDICADOR DE LOTAO DAS UGs - BARRA DE PROGRESSO
   =================================================== */

.lotacao-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6px;
  width: 100%;
  position: relative;
}

.lotacao-barra-fundo {
  width: 100%;
  height: 14px;
  background: #2a2a2a;
  border: 1px solid #444;
  border-radius: 7px;
  overflow: hidden;
  position: relative;
}

.lotacao-barra-preenchida {
  height: 100%;
  transition: all 0.4s ease-in-out;
  border-radius: 6px;
  position: relative;
}

.lotacao-barra-preenchida::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, transparent 0%, rgba(255,255,255,0.2) 50%, transparent 100%);
  border-radius: 6px;
}

.lotacao-info-texto {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2px;
}

.lotacao-percentual {
  font-size: 0.8rem !important;
  font-weight: 700 !important;
  font-family: monospace;
  margin: 0;
  line-height: 1;
}

.lotacao-status {
  font-size: 0.65rem !important;
  font-weight: 500 !important;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin: 0;
  line-height: 1;
}

/* Estados da barra baseados na lotao */
.lotacao-container[data-status="disponvel"] .lotacao-barra-preenchida {
  background: linear-gradient(90deg, #3b82f6 0%, #60a5fa 100%) !important;
  box-shadow: 0 0 8px rgba(59, 130, 246, 0.4);
}

.lotacao-container[data-status="disponvel"] .lotacao-percentual,
.lotacao-container[data-status="disponvel"] .lotacao-status {
  color: #3b82f6 !important;
}

.lotacao-container[data-status="quase-cheia"] .lotacao-barra-preenchida {
  background: linear-gradient(90deg, #ffc107 0%, #ffcd39 100%) !important;
  box-shadow: 0 0 8px rgba(255, 193, 7, 0.4);
}

.lotacao-container[data-status="quase-cheia"] .lotacao-percentual,
.lotacao-container[data-status="quase-cheia"] .lotacao-status {
  color: #ffc107 !important;
}

.lotacao-container[data-status="cheia"] .lotacao-barra-preenchida {
  background: linear-gradient(90deg, #dc3545 0%, #e55465 100%) !important;
  box-shadow: 0 0 8px rgba(220, 53, 69, 0.4);
}

.lotacao-container[data-status="cheia"] .lotacao-percentual,
.lotacao-container[data-status="cheia"] .lotacao-status {
  color: #dc3545 !important;
}

/* Animao de pulso para UGs cheias */
.lotacao-container[data-status="cheia"] .lotacao-barra-preenchida {
  animation: pulse-danger 2s infinite;
}

@keyframes pulse-danger {
  0% { box-shadow: 0 0 8px rgba(220, 53, 69, 0.4); }
  50% { box-shadow: 0 0 16px rgba(220, 53, 69, 0.8); }
  100% { box-shadow: 0 0 8px rgba(220, 53, 69, 0.4); }
}

/* Responsividade para mobile */
@media (max-width: 768px) {
  .lotacao-container {
    min-width: 90px;
  }

  .lotacao-barra-fundo {
    height: 12px;
  }

  .lotacao-percentual {
    font-size: 0.75rem !important;
  }

  .lotacao-status {
    font-size: 0.6rem !important;
  }

  /* Manter todas as colunas visveis - usar scroll horizontal */
  .lotacao-container {
    min-width: 90px !important;
  }

  .lotacao-barra-fundo {
    height: 10px !important;
  }

  .lotacao-percentual {
    font-size: 0.7rem !important;
  }

  .lotacao-status {
    font-size: 0.6rem !important;
  }
}

/* Tooltip nativo do browser - mais compatvel */
.lotacao-container[title] {
  cursor: help;
}


/* =======================================================
   BOTO INFO (EYE) - VISUALIZAR UCs
   ======================================================= */

.action-btn.info {
  background: rgba(23, 162, 184, 0.1);
  color: #17a2b8;
}

.action-btn.info:hover:not(:disabled) {
  background: rgba(23, 162, 184, 0.2);
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(23, 162, 184, 0.2);
}

/* =======================================================
   MODAL VISUALIZAR UCs
   ======================================================= */

.modal-visualizar-ucs {
  background: linear-gradient(135deg, #1e293b 0%, #0f172a 100%) !important;
  max-width: 800px !important;
  width: 90% !important;
}

.modal-body-visualizar-ucs {
  padding: 24px !important;
  max-height: 70vh;
  overflow-y: auto;
}

/* Header de informaes da UG */
.ug-info-header {
  display: flex;
  gap: 24px;
  padding: 16px;
  background: rgba(59, 130, 246, 0.1);
  border-radius: 8px;
  margin-bottom: 20px;
  border-left: 3px solid #3b82f6;
}

.ug-info-item {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
}

.ug-info-item strong {
  color: rgba(255, 255, 255, 0.95);
  margin-right: 6px;
}

/* Tabela de UCs */
.ucs-table-container {
  overflow-x: auto;
  border-radius: 8px;
  margin-bottom: 16px;
}

.ucs-table {
  width: 100%;
  border-collapse: collapse;
  background: rgba(255, 255, 255, 0.05);
}

.ucs-table thead {
  background: rgba(59, 130, 246, 0.2);
}

.ucs-table th {
  padding: 12px 16px;
  text-align: left;
  color: rgba(255, 255, 255, 0.95);
  font-weight: 600;
  border-bottom: 2px solid rgba(255, 255, 255, 0.1);
}

.ucs-table td {
  padding: 10px 16px;
  /* No definir cor aqui - permitir que spans dentro definam suas prprias cores */
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

/* Cor padro para texto direto dentro do td (que no est em span) */
.ucs-table td:not(:has(span)) {
  color: rgba(255, 255, 255, 0.85);
}

/* Garantir que spans sem classe especfica tenham a cor padro */
.ucs-table td span:not([class*="energia-alocada"]):not([class*="porcentagem"]) {
  color: rgba(255, 255, 255, 0.85);
}

.ucs-table tbody tr:hover {
  background: rgba(255, 255, 255, 0.03);
}

.ucs-table tfoot {
  background: rgba(59, 130, 246, 0.15);
  border-top: 2px solid rgba(59, 130, 246, 0.3);
}

.ucs-table tfoot td {
  padding: 14px 16px;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.95);
}

/* Input de edio de mdia */
.input-media-edicao {
  width: 100%;
  padding: 6px 10px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 6px;
  background: rgba(255, 255, 255, 0.08);
  color: white;
  font-size: 0.9rem;
  transition: all 0.3s ease;
}

.input-media-edicao:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.2);
  background: rgba(255, 255, 255, 0.12);
}

/* Porcentagens */
.porcentagem-valor {
  font-weight: 600;
  font-family: monospace;
}

.porcentagem-ok {
  color: #10b981;
}

.porcentagem-aviso {
  color: #f59e0b;
}

/* Energia Alocada - com cores baseadas na comparao com mdia */
.energia-alocada-valor {
  font-weight: 600 !important;
  font-family: monospace;
  font-size: 0.95rem;
}

.energia-alocada-menor {
  color: #dc3545 !important; /* Vermelho - receber menos */
}

.energia-alocada-igual {
  color: #ffc107 !important; /* Amarelo - receber igual */
}

.energia-alocada-maior {
  color: #3b82f6 !important; /* Azul - receber mais */
}

/* Aviso de porcentagem */
.aviso-porcentagem {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 16px;
  background: rgba(245, 158, 11, 0.15);
  border: 1px solid rgba(245, 158, 11, 0.3);
  border-radius: 8px;
  color: #fbbf24;
  font-size: 0.9rem;
}

/* Footer do modal */
.modal-visualizar-ucs .modal-footer {
  display: flex;
  justify-content: space-between;
  padding: 20px 24px !important;
  border-top: 1px solid rgba(255, 255, 255, 0.1) !important;
}

.footer-left,
.footer-right {
  display: flex;
  gap: 12px;
}

/* Modal de confirmao */
.modal-confirm-dialog {
  background: linear-gradient(135deg, #1e293b 0%, #0f172a 100%) !important;
  max-width: 450px !important;
  width: 90% !important;
}

.modal-confirm-dialog .common-modal-content {
  padding: 24px !important;
  color: rgba(255, 255, 255, 0.85);
}

.modal-confirm-dialog .common-modal-content p {
  margin-bottom: 12px;
  line-height: 1.6;
}

.modal-confirm-dialog .common-modal-content strong {
  color: rgba(255, 255, 255, 0.95);
}

/* Responsividade */
@media (max-width: 768px) {
  .modal-visualizar-ucs {
    width: 95% !important;
  }

  .ug-info-header {
    flex-direction: column;
    gap: 12px;
  }

  .ucs-table th,
  .ucs-table td {
    padding: 8px 12px;
    font-size: 0.85rem;
  }

  .modal-visualizar-ucs .modal-footer {
    flex-direction: column;
  }

  .footer-left,
  .footer-right {
    width: 100%;
    justify-content: stretch;
  }

  .footer-left .btn,
  .footer-right .btn {
    flex: 1;
  }
}

/* =======================================================
   ABAS - Tabs
   ======================================================= */

.tabs-container {
  display: flex;
  gap: 0;
  margin-bottom: 0;
  border-bottom: 2px solid rgba(255, 255, 255, 0.1);
}

.tab-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 24px;
  background: transparent;
  border: none;
  border-bottom: 3px solid transparent;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.95rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  margin-bottom: -2px;
}

.tab-button:hover {
  color: rgba(255, 255, 255, 0.9);
  background: rgba(255, 255, 255, 0.05);
}

.tab-button.active {
  color: #3b82f6;
  border-bottom-color: #3b82f6;
  background: rgba(59, 130, 246, 0.1);
}

.tab-button svg {
  opacity: 0.8;
}

.tab-button.active svg {
  opacity: 1;
}

/* =======================================================
   TABELA HISTRICO - Estilos especficos
   ======================================================= */

.data-valor {
  color: rgba(255, 255, 255, 0.85);
  font-size: 0.9rem;
}

.arquivo-nome {
  display: flex;
  align-items: center;
  gap: 6px;
  color: #3b82f6;
  font-size: 0.85rem;
}

.sem-arquivo {
  color: rgba(255, 255, 255, 0.4);
}

.numero-uc {
  color: rgba(255, 255, 255, 0.75);
  font-family: monospace;
  font-size: 0.9rem;
}

/* =======================================================
   MODAL RATEIO
   ======================================================= */

.modal-rateio {
  max-width: 600px !important;
  width: 95% !important;
}

.form-group-full {
  grid-column: 1 / -1;
}

.input-disabled {
  background: rgba(255, 255, 255, 0.05) !important;
  color: rgba(255, 255, 255, 0.6) !important;
  cursor: not-allowed;
}

.btn-edit-inline {
  background: transparent;
  border: none;
  color: #3b82f6;
  cursor: pointer;
  padding: 2px 6px;
  margin-left: 8px;
  border-radius: 4px;
  transition: all 0.2s;
}

.btn-edit-inline:hover {
  background: rgba(59, 130, 246, 0.2);
}

/* Upload de arquivo */
.file-upload-container {
  position: relative;
}

.file-input {
  position: absolute;
  width: 0;
  height: 0;
  opacity: 0;
  overflow: hidden;
}

.file-label {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 16px;
  background: rgba(255, 255, 255, 0.05);
  border: 2px dashed rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  transition: all 0.2s;
}

.file-label:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(59, 130, 246, 0.5);
  color: rgba(255, 255, 255, 0.9);
}

.file-atual {
  display: block;
  margin-top: 8px;
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.8rem;
}

/* Textarea no modal */
.modal-rateio textarea {
  width: 100%;
  padding: 12px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 0.9rem;
  resize: vertical;
  min-height: 80px;
}

.modal-rateio textarea:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.2);
}

.modal-rateio textarea::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

/* Table header com boto */
.table-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 0;
}

.table-header h2 {
  margin: 0;
}

/* =====================================================
   BOTO E MODAL ITENS DO RATEIO
   ===================================================== */

/* Boto para ver itens do rateio */
.btn-itens-rateio {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 6px 12px;
  background: rgba(59, 130, 246, 0.15);
  border: 1px solid rgba(59, 130, 246, 0.3);
  border-radius: 6px;
  color: #60a5fa;
  font-size: 0.85rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-itens-rateio:hover {
  background: rgba(59, 130, 246, 0.25);
  border-color: rgba(59, 130, 246, 0.5);
  color: #93c5fd;
}

.sem-itens {
  color: rgba(255, 255, 255, 0.4);
}

/* Modal itens do rateio */
.modal-itens-rateio {
  max-width: 700px !important;
  width: 95%;
}

.modal-itens-rateio .ucs-table-container {
  max-height: 400px;
  overflow-y: auto;
}

.modal-itens-rateio .ucs-table th:first-child,
.modal-itens-rateio .ucs-table td:first-child {
  width: 50px;
  text-align: center;
}

.modal-itens-rateio .porcentagem-ok {
  color: #22c55e;
}

.modal-itens-rateio .porcentagem-aviso {
  color: #f59e0b;
}

.modal-itens-rateio .sem-valor {
  color: rgba(255, 255, 255, 0.4);
}

.modal-itens-rateio .empty-state {
  padding: 40px 20px;
  text-align: center;
}

.modal-itens-rateio .empty-state h3 {
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 8px;
}

.modal-itens-rateio .empty-state p {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.9rem;
}
