/* src/pages/ControlePage.css - LARGURA CORRIGIDA */

/* Importar estilos padronizados de indicadores */
@import '../styles/indicators.css';

.page-container {
  min-height: 100vh;
  background: #1a1a1a !important; /*  MUDAR ESTA LINHA */
  background-attachment: fixed !important;
}

/* =======================================================
   RESPONSIVIDADE GERAL E NOTEBOOKS
   ======================================================= */

.container {
  width: 90%;         /* Desktop padro */
  margin: 0 auto;
  padding: 20px;
}

/* Responsividade especfica para notebooks (1024px - 1366px) */
@media (min-width: 1024px) and (max-width: 1366px) {
  .container {
    width: 95% !important;  /* Expandir para notebooks */
    padding: 15px 20px;     /* Ajustar padding */
  }

  .table th,
  .table td {
    padding: 10px 12px;     /* Otimizar espaamento */
    font-size: 0.9rem;      /* Ajustar fonte */
  }

  .table {
    min-width: 1400px;      /* Garantir espao para todas colunas do controle */
  }
}



/* =======================================================
   ELEMENTOS ESPECFICOS DA TABELA - VISIBILIDADE
   ======================================================= */

/* Clulas especficas com melhor contraste */
.cliente-info .nome-cliente,
.proposta-numero,
.uc-numero,
.consultor-nome {
  font-weight: 600;
  color: #f0f0f0 !important; /*  Texto bem claro */
}

.numero-proposta,
.numero-uc {
  font-family: monospace;
  background: #3d3d3d !important; /*  Fundo um tom mais claro */
  color: #f0f0f0 !important;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 0.9rem;
  border: 1px solid #555;
}

.consultor {
  color: #d0d0d0 !important; /*  Cinza claro */
  font-size: 0.9rem;
}

/* UG cells */
.ug-cell {
  display: flex;
  align-items: center;
  gap: 8px;
}

.ug-text {
  font-weight: 500;
  color: #f0f0f0 !important;
}

.ug-vazio .ug-text {
  color: #ff6b6b !important; /*  Vermelho mais claro para tema escuro */
  font-style: italic;
}

.ug-definido .ug-text {
  color: #51cf66 !important; /*  Verde mais claro para tema escuro */
}

.ug-status {
  color: #51cf66 !important; /*  Verde claro */
  font-size: 0.8rem;
}

/* Valores numricos */
.media-valor {
  font-family: monospace;
  font-weight: 600;
  color: #74c0fc !important; /*  Azul claro para contraste */
}

.calibragem-info {
  background: transparent !important; /* Remove fundo branco */
  padding: 4px 0 !important; /* Reduz padding vertical */
  margin: 0 !important; /* Remove margem */
  min-height: auto !important; /* Remove altura mnima */
  height: auto !important; /* Altura automtica */
}

.calibragem-resultado {
  font-family: monospace;
  font-weight: 600;
  color: #51cf66 !important; /*  Verde claro */
}

.sem-calibragem {
  color: #95a5a6 !important;
  font-style: italic !important;
  font-size: 0.85rem !important;
}

/* Textos pequenos e elementos secundrios */
small {
  color: #adb5bd !important; /*  Cinza claro para textos pequenos */
}

/* Status badges - se existirem */
.status-badge {
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 0.75rem;
  font-weight: 600;
}

.status-ativo {
  background: #51cf66 !important;
  color: #1a1a1a !important;
}

.status-inativo {
  background: #ff6b6b !important;
  color: #1a1a1a !important;
}

