/* =======================================================
   ESTATSTICAS RPIDAS - ESTILOS ESPECFICOS DA PGINA
   Os estilos base esto em ../styles/indicators.css
   ======================================================= */

/* Nenhum override necessrio - usar padro */

/* =======================================================
   SEO DE FILTROS
   ======================================================= */

.filters-section {
  background: white;
  border-radius: 15px;
  padding: 18px;
  margin-bottom: 18px;
  box-shadow: 0 5px 20px rgba(0,0,0,0.1);
  position: relative;
  z-index: 1;
}

.filters-container {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.filters-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 12px;
  align-items: end;
  margin-bottom: 12px;
}

/* Grid com calibragem - 5 colunas, buscar menor */
.filters-grid-with-calibragem {
  display: grid;
  grid-template-columns: 1.2fr 1fr 1fr 1fr 1fr;
  gap: 12px;
  align-items: end;
  margin-bottom: 12px;
}

.filter-busca {
  grid-column: span 1;
  order: -1;
}

/* Layout horizontal inline para calibragem */
.calibragem-inline-wrapper {
  display: flex;
  align-items: center;
  gap: 6px;
  flex-wrap: nowrap;
}

.calibragem-input-small {
  width: 50px;
  padding: 8px 6px;
  border: 2px solid #e1e1e1;
  border-radius: 6px;
  font-size: 0.9rem;
  transition: all 0.3s ease;
  background: white;
  text-align: center;
}

.calibragem-input-small:focus {
  outline: none;
  border-color: #2d7a31;
  box-shadow: 0 0 0 2px rgba(30, 90, 35, 0.1);
}

.percent-sign {
  font-size: 0.85rem;
  color: #f0f0f0;
  font-weight: 600;
  flex-shrink: 0;
}

.calibragem-atual-inline {
  font-size: 0.7rem;
  color: #f0f0f0;
  white-space: nowrap;
  flex-shrink: 0;
}

.btn-xs {
  padding: 6px 10px !important;
  font-size: 0.75rem !important;
  line-height: 1 !important;
  white-space: nowrap;
  flex-shrink: 0;
}

/* Linha de aes compacta - alinhada  direita */
.actions-row {
  display: flex;
  gap: 12px;
  align-items: center;
  justify-content: flex-end;
  margin-bottom: 0;
}

.calibragem-info-compact {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 8px 12px;
  background: rgba(30, 90, 35, 0.1);
  border-radius: 8px;
  border: 1px solid rgba(30, 90, 35, 0.2);
}

.actions-container {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  align-items: center;
}

.btn-sm {
  padding: 8px 16px !important;
  font-size: 0.85rem !important;
}

.filter-group {
  display: flex;
  flex-direction: column;
}

.filter-group label {
  font-weight: 600;
  margin-bottom: 8px;
  color: #333;
  font-size: 0.9rem;
}

.filter-group input,
.filter-group select {
  padding: 12px 15px;
  border: 2px solid #e1e1e1;
  border-radius: 8px;
  font-size: 0.95rem;
  transition: all 0.3s ease;
  background: white;
}

.filter-group input:focus,
.filter-group select:focus {
  outline: none;
  border-color: #2d7a31;
  box-shadow: 0 0 0 3px rgba(30, 90, 35, 0.1);
}

.calibragem-controls {
  display: flex;
  justify-content: space-between;
  align-items: end;
  gap: 20px;
  padding: 20px 0;
  border-top: 1px solid #e1e1e1;
}

.calibragem-group {
  display: flex;
  align-items: end;
  gap: 15px;
}

.calibragem-group label {
  font-weight: 600;
  color: #333;
  margin-bottom: 5px;
  font-size: 0.9rem;
}

.calibragem-group input {
  padding: 10px 12px;
  border: 2px solid #e1e1e1;
  border-radius: 6px;
  width: 120px;
  font-size: 0.95rem;
}

/* =======================================================
   TABELA - FORMATAO IDNTICA AO PROSPEC (CORRIGIDA)
   ======================================================= */

.table-section {
  background: white;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
  margin-top: 30px;
}

.table-header {
  background: #2d2d2d !important;  /*  Fundo escuro igual ao Prospec */
  padding: 20px 25px;
  border-bottom: 2px solid #e1e1e1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.table-header h2 {
  margin: 0;
  color: #f0f0f0 !important;  /*  Texto claro no ttulo */
  font-size: 1.3rem;
  display: flex;
  align-items: center;
  gap: 10px;
}

.table-count {
  background: #2d7a31;
  color: white;
  padding: 5px 12px;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 500;
  line-height: 1.3;
  text-align: center;
}

/* Container da tabela com scroll */
.table-container {
  overflow-x: auto;
  max-height: 70vh;
  position: relative;
  background: #2d2d2d !important;
}

/* Tabela - OVERRIDE TOTAL com mxima especificidade */
.page-container .container .table-section .table-container .table,
.page-container .table-section .table {
  width: 100% !important;
  border-collapse: collapse !important;
  font-size: 0.85rem !important;
  background: #2d2d2d !important;
  min-width: 1000px !important;
}

/* Cabealho da tabela - OVERRIDE TOTAL */
.page-container .container .table-section .table thead,
.page-container .table-section .table thead {
  position: sticky !important;
  top: 0 !important;
  background: #333 !important;
  z-index: 10 !important;
}

.page-container .container .table-section .table th,
.page-container .table-section .table th {
  background: #333 !important;
  padding: 15px 12px !important;
  text-align: left !important;
  font-weight: 600 !important;
  color: #f0f0f0 !important;
  border-bottom: 2px solid #555 !important;
  font-size: 0.85rem !important;
  white-space: nowrap !important;
  min-width: 100px !important;
  cursor: pointer !important;
  user-select: none !important;
  transition: background-color 0.3s ease !important;
}

.page-container .container .table-section .table th:hover,
.page-container .table-section .table th:hover {
  background: #404040 !important;
}

/* Clulas da tabela - OVERRIDE TOTAL */
.page-container .container .table-section .table td,
.page-container .table-section .table td {
  padding: 12px !important;
  border-bottom: 1px solid #444 !important;
  font-size: 0.85rem !important;
  vertical-align: middle !important;
  min-width: 80px !important;
  color: #f0f0f0 !important;
  background: #2d2d2d !important;
}

/* HOVER e ZEBRA - OVERRIDE TOTAL com todas as especificidades possveis */
.page-container .container .table-section .table tbody tr,
.page-container .table-section .table tbody tr,
.page-container .container .table-section .table tr,
.page-container .table-section .table tr,
.table-section .table tbody tr,
.table tbody tr,
.table tr {
  background: #2d2d2d !important;
}

.page-container .container .table-section .table tbody tr:nth-child(even),
.page-container .table-section .table tbody tr:nth-child(even),
.page-container .container .table-section .table tr:nth-child(even),
.page-container .table-section .table tr:nth-child(even),
.table-section .table tbody tr:nth-child(even),
.table tbody tr:nth-child(even),
.table tr:nth-child(even) {
  background: #2d2d2d !important;  /*  MESMO fundo para todas as linhas */
}

.page-container .container .table-section .table tbody tr:hover,
.page-container .table-section .table tbody tr:hover,
.page-container .container .table-section .table tr:hover,
.page-container .table-section .table tr:hover,
.table-section .table tbody tr:hover,
.table tbody tr:hover,
.table tr:hover {
  background: #3d3d3d !important;  /*  MESMO hover para todas as linhas */
}

.page-container .container .table-section .table tbody tr:nth-child(even):hover,
.page-container .table-section .table tbody tr:nth-child(even):hover,
.page-container .container .table-section .table tr:nth-child(even):hover,
.page-container .table-section .table tr:nth-child(even):hover,
.table-section .table tbody tr:nth-child(even):hover,
.table tbody tr:nth-child(even):hover,
.table tr:nth-child(even):hover {
  background: #3d3d3d !important;  /*  MESMO hover para linhas pares */
}

/* =======================================================
   ESTADOS DA PGINA
   ======================================================= */

.loading-state,
.empty-state {
  text-align: center;
  padding: 60px 20px;
  color: #666;
}

.loading-state .spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #f3f3f3;
  border-top: 4px solid #2d7a31;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 20px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.empty-icon {
  font-size: 4rem;
  margin-bottom: 20px;
  opacity: 0.5;
}

.empty-state h3 {
  margin-bottom: 10px;
  color: #495057;
}

.empty-state p {
  margin-bottom: 20px;
}

