}

/* Footer do modal - Dark Mode */
.modal-footer-controle {
  padding: 20px 24px !important;
  border-top: 1px solid rgba(255, 255, 255, 0.1) !important;
  background: rgba(255, 255, 255, 0.03) !important;
  border-radius: 0 0 16px 16px !important;
  display: flex !important;
  justify-content: flex-end !important;
  gap: 12px !important;
}

/* Boto fechar */
.modal-controle .btn-close {
  background: none !important;
  border: none !important;
  font-size: 1.5rem !important;
  cursor: pointer !important;
  color: #666 !important;
  padding: 0 !important;
  width: 30px !important;
  height: 30px !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  border-radius: 50% !important;
  transition: all 0.2s ease !important;
}

.modal-controle .btn-close:hover {
  background: #e9ecef !important;
  color: #333 !important;
}

/* Botes do modal - Dark Mode */
.modal-controle .btn,
.modal-footer-controle .btn {
  padding: 14px 20px !important;
  border: none !important;
  border-radius: 8px !important;
  font-size: 1rem !important;
  font-weight: 600 !important;
  cursor: pointer !important;
  transition: all 0.3s ease !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  gap: 8px !important;
}

.modal-controle .btn-primary,
.modal-footer-controle .btn-primary {
  background: rgba(255, 255, 255, 0.1) !important;
  color: rgba(255, 255, 255, 0.9) !important;
  border: 1px solid rgba(255, 255, 255, 0.2) !important;
}

.modal-controle .btn-primary:hover:not(:disabled),
.modal-footer-controle .btn-primary:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.15) !important;
  transform: translateY(-2px) !important;
  border-color: rgba(255, 255, 255, 0.3) !important;
}

.modal-controle .btn-secondary,
.modal-footer-controle .btn-secondary {
  background: rgba(255, 255, 255, 0.1) !important;
  color: rgba(255, 255, 255, 0.9) !important;
  border: 1px solid rgba(255, 255, 255, 0.2) !important;
}

.modal-controle .btn-secondary:hover,
.modal-footer-controle .btn-secondary:hover {
  background: rgba(255, 255, 255, 0.15) !important;
  transform: translateY(-2px) !important;
}

.modal-controle .btn-danger {
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%) !important;
  color: white !important;
}

.modal-controle .btn-danger:hover {
  transform: translateY(-2px) !important;
  box-shadow: 0 8px 20px rgba(239, 68, 68, 0.4) !important;
}

.modal-controle .btn:disabled,
.modal-footer-controle .btn:disabled {
  opacity: 0.6 !important;
  cursor: not-allowed !important;
  transform: none !important;
}

/* Animao do modal */
.modal-controle {
  animation: modalSlideIn 0.3s ease-out !important;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* =======================================================
   RESPONSIVIDADE
   ======================================================= */

@media (max-width: 1300px) {
  .container {
    max-width: 1100px;
    width: 88%;
  }
}

@media (max-width: 1200px) {
  .quick-stats {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .container {
    width: 95%;
    padding: 15px;
  }

  .quick-stats {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  
  .filters-section {
    padding: 20px 15px;
  }
  
  .filters-grid {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  
  .calibragem-controls {
    flex-direction: column;
    align-items: stretch;
    gap: 15px;
  }
  
  .calibragem-group {
    flex-direction: column;
    align-items: stretch;
    gap: 10px;
  }
  
  .calibragem-group input {
    width: 100%;
  }
  
  .table {
    min-width: 800px;
  }
  
  .table th,
  .table td {
    padding: 8px 6px;
    font-size: 0.8rem;
  }
  
  .modal-controle {
    width: 95% !important;
    max-width: none !important;
    margin: 10px !important;
  }

  .modal-header-controle,
  .modal-body-controle,
  .modal-footer-controle {
    padding: 16px !important;
  }
}

@media (max-width: 480px) {
  .container {
    width: 98%;
    padding: 10px;
  }
  
  .table th,
  .table td {
    padding: 6px 4px;
    font-size: 0.75rem;
  }
  
  .btn-small {
    padding: 4px 8px;
    font-size: 0.7rem;
  }
}

/* ===============================================
   AJUSTES NA TABELA PARA MELHOR ALINHAMENTO
   =============================================== */

/* Ajustar altura das clulas da tabela */
.table td {
  vertical-align: middle !important; /* Centralizar verticalmente */
  padding: 12px 8px !important; /* Padding consistente */
  line-height: 1.3 !important; /* Altura da linha */
}

/* Coluna de status especificamente */
.table td:has(.status-troca-btn) {
  text-align: center !important;
  padding: 8px !important;
}

/* Coluna calibrada especificamente */
.table td:has(.calibragem-info) {
  padding: 8px !important;
  vertical-align: middle !important;
}

/* ===============================================
   CALIBRAGEM INDIVIDUAL - CONTROLE
   =============================================== */

.calibragem-individual {
  display: flex;
  align-items: center;
  gap: 5px;
  margin-top: 5px;
}

.calibragem-individual input {
  width: 80px !important;
  padding: 4px 6px !important;
  font-size: 0.85rem !important;
  border: 1px solid #ddd !important;
  border-radius: 4px !important;
  text-align: center;
}

.calibragem-individual input:focus {
