/*
 * CommonModal.css
 * CSS compartilhado para todos os modais do sistema
 * Baseado no padro do SettingsModal
 */

/* ===============================================
   OVERLAY E ESTRUTURA BASE
   =============================================== */

.common-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  animation: fadeIn 0.2s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.common-modal {
  background: linear-gradient(135deg, #1e293b 0%, #0f172a 100%);
  border-radius: 16px;
  width: 90%;
  max-width: 600px;
  max-height: 90vh;
  overflow: hidden;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
  animation: slideUp 0.3s ease;
  display: flex;
  flex-direction: column;
}

/* Variante para modais maiores */
.common-modal.large {
  max-width: 800px;
}

/* Variante para modais menores */
.common-modal.small {
  max-width: 400px;
}

@keyframes slideUp {
  from {
    transform: translateY(50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* ===============================================
   HEADER DO MODAL
   =============================================== */

.common-modal-header {
  padding: 24px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: rgba(255, 255, 255, 0.03);
  flex-shrink: 0;
}

.common-modal-header h2,
.common-modal-header h3 {
  margin: 0;
  color: white;
  font-size: 1.5rem;
  font-weight: 700;
  display: flex;
  align-items: center;
  gap: 10px;
}

.common-close-btn {
  background: transparent;
  border: none;
  color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
  padding: 8px;
  border-radius: 8px;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.common-close-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  color: white;
}

/* ===============================================
   TABS (ABAS)
   =============================================== */

.common-modal-tabs {
  display: flex;
  gap: 8px;
  padding: 16px 24px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  flex-shrink: 0;
}

.common-modal-tab {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 20px;
  background: transparent;
  border: none;
  color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
  border-radius: 8px 8px 0 0;
  font-size: 0.95rem;
  font-weight: 500;
  transition: all 0.3s ease;
  position: relative;
}

.common-modal-tab:hover {
  background: rgba(255, 255, 255, 0.05);
  color: rgba(255, 255, 255, 0.9);
}

.common-modal-tab.active {
  background: rgba(255, 255, 255, 0.1);
  color: white;
}

.common-modal-tab.active::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, #3b82f6, #8b5cf6);
}

/* Alias para modal-tabs (usar em modais especficos) */
.modal-tabs {
  display: flex;
  gap: 8px;
  padding: 16px 24px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  flex-shrink: 0;
  background: rgba(0, 0, 0, 0.2);
}

.modal-tab {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 20px;
  background: transparent;
  border: none;
  color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
  border-radius: 8px 8px 0 0;
  font-size: 0.9rem;
  font-weight: 500;
  transition: all 0.3s ease;
  position: relative;
}

.modal-tab:hover {
  background: rgba(255, 255, 255, 0.05);
  color: rgba(255, 255, 255, 0.9);
}

.modal-tab.active {
  background: rgba(255, 255, 255, 0.1);
  color: white;
}

.modal-tab.active::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, #3b82f6, #8b5cf6);
}

.modal-tab svg {
  width: 16px;
  height: 16px;
}

/* ===============================================
   CONTEDO DO MODAL
   =============================================== */

.common-modal-content {
  padding: 24px;
  overflow-y: auto;
  flex: 1;
}

/* Sees dentro do contedo */
.common-modal-section {
  margin-bottom: 24px;
}

.common-modal-section:last-child {
  margin-bottom: 0;
}

.common-modal-section h3 {
  color: white;
  font-size: 1.1rem;
  font-weight: 600;
  margin: 0 0 20px 0;
}

.common-modal-section p {
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.9rem;
  line-height: 1.5;
  margin: 0 0 16px 0;
}

/* ===============================================
   CAMPOS DE FORMULRIO
   =============================================== */

.common-field {
  margin-bottom: 20px;
}

.common-field label {
  display: flex;
  align-items: center;
  gap: 8px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 0.9rem;
  font-weight: 500;
  margin-bottom: 8px;
}

.common-input,
.common-select,
.common-textarea {
  width: 100%;
  padding: 12px 16px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  color: white;
  font-size: 0.95rem;
  transition: all 0.3s ease;
  box-sizing: border-box;
}

.common-input:focus,
.common-select:focus,
.common-textarea:focus {
  outline: none;
  border-color: #3b82f6;
  background: rgba(255, 255, 255, 0.08);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.common-input:disabled,
.common-select:disabled,
.common-textarea:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  background: rgba(255, 255, 255, 0.02);
}

.common-textarea {
  min-height: 100px;
  resize: vertical;
}

/* Input com wrapper (para cone de senha, etc) */
.common-input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.common-input-wrapper input {
  padding-right: 48px;
}

.common-input-icon {
  position: absolute;
  right: 12px;
  background: transparent;
  border: none;
  color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  padding: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  transition: all 0.3s ease;
}

.common-input-icon:hover {
  color: rgba(255, 255, 255, 0.9);
  background: rgba(255, 255, 255, 0.05);
}

/* ===============================================
   MENSAGENS
   =============================================== */

.common-message {
  padding: 12px 16px;
  border-radius: 8px;
  margin-bottom: 16px;
  font-size: 0.9rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 10px;
}

.common-message.success {
  background: rgba(34, 197, 94, 0.1);
  color: #4ade80;
  border: 1px solid rgba(34, 197, 94, 0.3);
}

.common-message.error {
  background: rgba(239, 68, 68, 0.1);
  color: #f87171;
  border: 1px solid rgba(239, 68, 68, 0.3);
}

.common-message.warning {
  background: rgba(251, 191, 36, 0.1);
  color: #fbbf24;
  border: 1px solid rgba(251, 191, 36, 0.3);
}

.common-message.info {
  background: rgba(59, 130, 246, 0.1);
  color: #60a5fa;
  border: 1px solid rgba(59, 130, 246, 0.3);
}

/* ===============================================
   BOTES
   =============================================== */

.common-btn {
  padding: 14px 24px;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  border: none;
}

.common-btn-primary {
  background: linear-gradient(135deg, #3b82f6 0%, #8b5cf6 100%);
  color: white;
}

.common-btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(59, 130, 246, 0.4);
}

.common-btn-secondary {
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.9);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.common-btn-secondary:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 255, 255, 0.3);
}

.common-btn-danger {
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
  color: white;
}

.common-btn-danger:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(239, 68, 68, 0.4);
}

.common-btn-success {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  color: white;
}

.common-btn-success:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(16, 185, 129, 0.4);
}

.common-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none !important;
  box-shadow: none !important;
}

/* Botes pequenos */
.common-btn-sm {
  padding: 8px 16px;
  font-size: 0.875rem;
}

/* ===============================================
   FOOTER DO MODAL
   =============================================== */

.common-modal-footer {
  padding: 20px 24px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.03);
  display: flex;
  gap: 12px;
  justify-content: flex-end;
  flex-shrink: 0;
}

/* ===============================================
   INFO BOX / ALERT BOX
   =============================================== */

.common-info-box {
  background: rgba(255, 255, 255, 0.03);
  border-left: 3px solid #3b82f6;
  border-radius: 8px;
  padding: 12px 16px;
  margin: 16px 0;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.85rem;
  line-height: 1.5;
}

.common-info-box.warning {
  border-left-color: #fbbf24;
}

.common-info-box.error {
  border-left-color: #ef4444;
}

.common-info-box.success {
  border-left-color: #10b981;
}

/* ===============================================
   CARDS INFORMATIVOS
   =============================================== */

.common-info-cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 16px;
  margin: 20px 0;
}

.common-info-card {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 16px;
  display: flex;
  align-items: center;
  gap: 12px;
  transition: all 0.3s ease;
}

.common-info-card:hover {
  background: rgba(255, 255, 255, 0.08);
  border-color: rgba(255, 255, 255, 0.2);
}

.common-info-card-icon {
  width: 40px;
  height: 40px;
  border-radius: 10px;
  background: rgba(59, 130, 246, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #3b82f6;
  flex-shrink: 0;
}

.common-info-card-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.common-info-card-label {
  font-size: 0.75rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.6);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.common-info-card-value {
  font-size: 1rem;
  font-weight: 600;
  color: white;
}

/* ===============================================
   LOADING STATE
   =============================================== */

.common-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px;
  color: rgba(255, 255, 255, 0.8);
  gap: 16px;
}

.common-loading-spinner {
  width: 40px;
  height: 40px;
  border: 3px solid rgba(255, 255, 255, 0.1);
  border-top-color: #3b82f6;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* ===============================================
   EMPTY STATE
   =============================================== */

.common-empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px 20px;
  text-align: center;
  color: rgba(255, 255, 255, 0.6);
  gap: 16px;
}

.common-empty-state svg {
  color: rgba(255, 255, 255, 0.3);
}

.common-empty-state h3 {
  margin: 0;
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.8);
}

.common-empty-state p {
  margin: 0;
  font-size: 0.95rem;
  line-height: 1.5;
  max-width: 400px;
}

/* ===============================================
   RESPONSIVIDADE
   =============================================== */

@media (max-width: 768px) {
  .common-modal {
    width: 95%;
    max-height: 95vh;
  }

  .common-modal-header {
    padding: 20px;
  }

  .common-modal-header h2,
  .common-modal-header h3 {
    font-size: 1.3rem;
  }

  .common-modal-tabs {
    padding: 12px 20px 0;
  }

  .common-modal-tab {
    padding: 10px 16px;
    font-size: 0.9rem;
  }

  .common-modal-content {
    padding: 20px;
  }

  .common-info-cards {
    grid-template-columns: 1fr;
  }

  .common-modal-footer {
    flex-direction: column-reverse;
  }

  .common-modal-footer .common-btn {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .common-modal-header {
    padding: 16px;
  }

  .common-modal-header h2,
  .common-modal-header h3 {
    font-size: 1.2rem;
  }

  .common-modal-content {
    padding: 16px;
  }
}
