/* =======================================================
   BOTES GLOBAIS - DARK MODE
   Sistema unificado de botes para toda a aplicao
   ======================================================= */

/* Classe base para todos os botes */
.btn {
  padding: 14px 20px;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  white-space: nowrap;
}

/* Garantir alinhamento vertical de cones em botes */
.btn svg,
.btn-primary svg,
.btn-secondary svg,
.btn-danger svg,
.btn-success svg,
.btn-warning svg,
.btn-info svg {
  vertical-align: middle;
  flex-shrink: 0;
}

.btn:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.3);
}

/* Boto Primrio - Fundo semi-transparente */
.btn-primary {
  background: rgba(255, 255, 255, 0.1) !important;
  color: rgba(255, 255, 255, 0.9) !important;
  border: 1px solid rgba(255, 255, 255, 0.2) !important;
}

.btn-primary:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.15) !important;
  transform: translateY(-2px);
  border-color: rgba(255, 255, 255, 0.3) !important;
}

.btn-primary svg {
  color: rgba(255, 255, 255, 0.9) !important;
  filter: brightness(1.2);
}

/* Boto Secundrio - Semi-transparente */
.btn-secondary {
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.9);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.btn-secondary:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 255, 255, 0.3);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(255, 255, 255, 0.1);
}

.btn-secondary svg {
  color: rgba(255, 255, 255, 0.9);
  filter: brightness(1.2);
}

/* Boto Danger - Gradient vermelho */
.btn-danger {
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
  color: white;
}

.btn-danger:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(239, 68, 68, 0.4);
}

.btn-danger svg {
  color: white;
  filter: brightness(1.2);
}

/* Boto Success - Gradient azul */
.btn-success {
  background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
  color: white;
}

.btn-success:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(59, 130, 246, 0.4);
}

.btn-success svg {
  color: white;
  filter: brightness(1.2);
}

/* Boto Warning - Gradient amarelo */
.btn-warning {
  background: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);
  color: white;
}

.btn-warning:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(245, 158, 11, 0.4);
}

.btn-warning svg {
  color: white;
  filter: brightness(1.2);
}

/* Boto Info - Gradient ciano */
.btn-info {
  background: linear-gradient(135deg, #06b6d4 0%, #0891b2 100%);
  color: white;
}

.btn-info:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(6, 182, 212, 0.4);
}

.btn-info svg {
  color: white;
  filter: brightness(1.2);
}

/* Estados dos botes */
.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none !important;
  box-shadow: none !important;
}

.btn:active:not(:disabled) {
  transform: translateY(0);
}

/* Tamanhos de botes */
.btn-sm {
  padding: 10px 16px;
  font-size: 0.875rem;
  gap: 6px;
}

.btn-lg {
  padding: 16px 28px;
  font-size: 1.125rem;
  gap: 10px;
}

/* Boto de largura total */
.btn-block {
  width: 100%;
  justify-content: center;
}

/* Boto com cone apenas */
.btn-icon {
  width: 40px;
  height: 40px;
  padding: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.btn-icon-sm {
  width: 32px;
  height: 32px;
}

.btn-icon-lg {
  width: 48px;
  height: 48px;
}

/* Boto ghost/outline */
.btn-ghost {
  background: transparent;
  border: 2px solid rgba(255, 255, 255, 0.2);
  color: rgba(255, 255, 255, 0.9);
}

.btn-ghost:hover:not(:disabled) {
  border-color: #3b82f6;
  color: #3b82f6;
  background: rgba(59, 130, 246, 0.1);
}

/* Grupo de botes */
.btn-group {
  display: flex;
  gap: 12px;
  align-items: center;
}

.btn-group-vertical {
  flex-direction: column;
}

/* Responsividade */
@media (max-width: 768px) {
  .btn {
    padding: 12px 18px;
    font-size: 0.9rem;
  }

  .btn-group {
    flex-wrap: wrap;
    gap: 8px;
  }

  .btn-block-mobile {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .btn {
    padding: 10px 16px;
    font-size: 0.85rem;
  }
}
