/* ModalConsultorDetalhes.css - Estilos especficos do modal de consultor */

/* ===============================================
   ABA DE INFORMAES - LAYOUT ESPECFICO
   =============================================== */

.tab-content-informacoes {
  padding: 0;
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
  margin-bottom: 32px;
}

.info-cards-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 16px;
  margin: 20px 0;
}

.info-card {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 20px;
  display: flex;
  align-items: center;
  gap: 16px;
  transition: all 0.2s ease;
}

.info-card:hover {
  border-color: #3b82f6;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.1);
}

.info-card.full-width {
  grid-column: 1 / -1;
}

.info-icon {
  width: 44px;
  height: 44px;
  background: rgba(59, 130, 246, 0.2);
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #3b82f6;
  flex-shrink: 0;
}

.info-details {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.info-details label {
  font-size: 0.75rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.6);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin: 0;
}

.info-details span {
  font-size: 1rem;
  font-weight: 600;
  color: white;
  word-break: break-word;
}

.status-badge {
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  width: fit-content;
}

.status-badge.active {
  background: rgba(16, 185, 129, 0.2);
  color: #10b981;
  border: 1px solid rgba(16, 185, 129, 0.3);
}

.status-badge.inactive {
  background: rgba(239, 68, 68, 0.2);
  color: #ef4444;
  border: 1px solid rgba(239, 68, 68, 0.3);
}

.user-id {
  font-family: 'Courier New', monospace;
  font-size: 0.9rem !important;
  color: rgba(255, 255, 255, 0.6) !important;
}

/* ===============================================
   AES DE EDIO
   =============================================== */

.info-actions {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 20px;
}

.btn-editar-consultor,
.btn-salvar,
.btn-cancelar {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  border-radius: 6px;
  font-weight: 600;
  cursor: pointer;
  border: none;
  font-size: 0.9rem;
  transition: all 0.3s ease;
}

.btn-editar-consultor {
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.9);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.btn-editar-consultor:hover {
  background: rgba(255, 255, 255, 0.15);
  transform: translateY(-2px);
  border-color: rgba(255, 255, 255, 0.3);
}

.btn-salvar {
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.9);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.btn-salvar:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.15);
  transform: translateY(-2px);
  border-color: rgba(255, 255, 255, 0.3);
}

.btn-salvar:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none !important;
}

.btn-cancelar {
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.9);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.btn-cancelar:hover {
  background: rgba(255, 255, 255, 0.15);
}

.edit-actions {
  display: flex;
  gap: 8px;
}

.edit-input {
  width: 100%;
  padding: 8px 12px;
  border: 2px solid #3b82f6;
  border-radius: 6px;
  font-size: 1rem;
  background: rgba(255, 255, 255, 0.05);
  color: white;
  transition: all 0.3s ease;
}

.edit-input:focus {
  outline: none;
  border-color: #60a5fa;
  background: rgba(255, 255, 255, 0.08);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.spinning {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

/* ===============================================
   BACKEND ERROR WARNING
   =============================================== */

.backend-error-warning {
  background: rgba(251, 191, 36, 0.1);
  border-left: 3px solid #fbbf24;
  border-radius: 8px;
  padding: 12px 16px;
  margin-bottom: 20px;
  color: #fbbf24;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  gap: 12px;
}

/* ===============================================
   ABA DE EQUIPE
   =============================================== */

.tab-content-equipe {
  padding: 0;
}

.equipe-section {
  margin-bottom: 32px;
}

.equipe-section:last-child {
  margin-bottom: 0;
}

.equipe-section h3 {
  display: flex;
  align-items: center;
  gap: 10px;
  margin: 0 0 20px 0;
  font-size: 1.2rem;
  font-weight: 700;
  color: white;
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.members-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 20px;
  justify-content: center;
  justify-items: center;
}

.member-card {
  min-height: 120px;
  height: fit-content;
  width: 100%;
  background: rgba(255, 255, 255, 0.05);
  border: 2px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 16px;
  transition: all 0.2s ease;
}

.member-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
}

.member-card.gerente {
  border-color: rgba(251, 146, 60, 0.3);
}

.member-card.gerente:hover {
  border-color: #fb923c;
  box-shadow: 0 8px 25px rgba(251, 146, 60, 0.2);
}

.member-card.vendedor {
  border-color: rgba(16, 185, 129, 0.3);
}

.member-card.vendedor:hover {
  border-color: #10b981;
  box-shadow: 0 8px 25px rgba(16, 185, 129, 0.2);
}

.member-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 12px;
}

.member-icon {
  width: 40px;
  height: 40px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.member-card.gerente .member-icon {
  background: rgba(251, 146, 60, 0.2);
  color: #fb923c;
}

.member-card.vendedor .member-icon {
  background: rgba(16, 185, 129, 0.2);
  color: #10b981;
}

.member-info {
  flex: 1;
}

.member-info h4 {
  margin: 0 0 2px 0;
  font-size: 1.1rem;
  font-weight: 700;
  color: white;
}

.member-role {
  font-size: 0.85rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.6);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.member-contact {
  display: flex;
  flex-direction: column;
  gap: 4px;
  margin-bottom: 12px;
}

.member-contact span {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
}

.expand-btn {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.7);
  padding: 4px;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.expand-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  color: white;
}

/* ===============================================
   VENDEDORES DO GERENTE
   =============================================== */

.gerente-vendedores {
  background: rgba(255, 255, 255, 0.03);
  border-radius: 8px;
  padding: 12px;
  margin-top: 12px;
}

.vendedores-label {
  display: block;
  font-size: 0.85rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 8px;
}

.vendedores-list {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.vendedor-item {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.85rem;
  padding: 8px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 4px;
  transition: all 0.2s ease;
}

.vendedor-item:last-child {
  border-bottom: none;
}

.vendedor-item:hover {
  background: rgba(255, 255, 255, 0.05);
}

.vendedor-item svg {
  color: #10b981;
  flex-shrink: 0;
}

.vendedor-info {
  display: flex;
  flex-direction: column;
  gap: 2px;
  flex: 1;
}

.vendedor-nome {
  font-weight: 600;
  color: white;
}

.vendedor-email {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.6);
}

.show-more-container {
  margin-top: 8px;
  text-align: center;
}

.show-more-btn {
  background: rgba(59, 130, 246, 0.1);
  border: 1px solid rgba(59, 130, 246, 0.3);
  color: #60a5fa;
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 0.85rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}

.show-more-btn:hover {
  background: rgba(59, 130, 246, 0.2);
  border-color: rgba(59, 130, 246, 0.5);
}

/* ===============================================
   ESTADO VAZIO
   =============================================== */

.empty-team-state {
  text-align: center;
  padding: 60px 40px;
  color: rgba(255, 255, 255, 0.6);
}

.empty-team-state svg {
  color: rgba(255, 255, 255, 0.3);
  margin-bottom: 16px;
}

.empty-team-state h3 {
  margin: 0 0 8px 0;
  font-size: 1.3rem;
  color: rgba(255, 255, 255, 0.8);
}

.empty-team-state p {
  margin: 0;
  font-size: 1rem;
  line-height: 1.5;
}

.loading-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px 20px;
  gap: 16px;
}

.loading-state svg {
  animation: spin 1s linear infinite;
  color: #3b82f6;
}

.loading-state p {
  color: rgba(255, 255, 255, 0.8);
  font-size: 1rem;
}

/* ===============================================
   RESPONSIVIDADE
   =============================================== */

@media (max-width: 768px) {
  .info-grid,
  .info-cards-grid {
    grid-template-columns: 1fr;
    gap: 16px;
  }

  .members-grid {
    grid-template-columns: 1fr;
  }

  .member-card {
    min-height: auto;
  }

  .edit-actions {
    flex-direction: column;
  }

  .btn-editar-consultor,
  .btn-salvar,
  .btn-cancelar {
    width: 100%;
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .tab-content-informacoes,
  .tab-content-equipe {
    padding: 0;
  }

  .info-card {
    padding: 16px;
  }

  .empty-team-state {
    padding: 40px 20px;
  }
}
