/* NovaPropostaPage.css - LAYOUT CORRIGIDO COM TAMANHOS UNIFORMES */

.page-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #2c3e50 0%, #34495e 100%);
  background-attachment: fixed;
  width: 100%;
  overflow-x: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
}

.form-container {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
  box-sizing: border-box;
}

/* =======================================================
   SEES DO FORMULRIO
   ======================================================= */

.form-section {
  background: #f8f9fa;
  border-radius: 12px;
  padding: 25px;
  margin-bottom: 25px;
  border-left: 4px solid #2d7a31;
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
}

.form-section h2 {
  color: #333;
  margin-bottom: 20px;
  font-size: 1.3rem;
  display: flex;
  align-items: center;
  gap: 10px;
}

/* =======================================================
   GRID UNIFORME - ADAPTVEL BASEADO NO ROLE
   ======================================================= */

.form-grid-uniform {
  display: grid;
  gap: 20px;
  align-items: start;
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
  margin-bottom: 20px;
}

/* Grid com 4 campos (Admin e Consultor) - COM recorrncia */
.form-grid-uniform {
  grid-template-columns: repeat(4, 1fr);
}

/* Grid com 3 campos (Gerente e Vendedor) - SEM recorrncia */
@media screen {
  /* Quando h apenas 3 campos visveis, usar grid de 3 colunas */
  body[data-user-role="gerente"] .form-grid-uniform,
  body[data-user-role="vendedor"] .form-grid-uniform {
    grid-template-columns: repeat(3, 1fr);
  }
}

/* Alternativa usando CSS puro - detectar automaticamente */
.form-grid-uniform:has(.form-group:nth-child(4)) {
  grid-template-columns: repeat(4, 1fr);
}

.form-grid-uniform:not(:has(.form-group:nth-child(4))) {
  grid-template-columns: repeat(3, 1fr);
}

/* Fallback para navegadores sem suporte ao :has() */
@supports not (selector(:has(.form-group))) {
  .form-grid-uniform {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    max-width: 100%;
  }
}

/* Responsividade */
@media (max-width: 1200px) {
  .form-grid-uniform {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .form-grid-uniform {
    grid-template-columns: 1fr;
  }
}

/* Grid com 3 campos quando recorrncia est oculta (Gerente e Vendedor) */
.form-grid-uniform:has(.form-group:nth-child(2):not(:empty)) {
  grid-template-columns: repeat(4, 1fr);
}

/* Fallback para quando no h suporte ao :has() */
@supports not (selector(:has(.form-group))) {
  .form-grid-uniform {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
}

/* Grid padro para outras sees */
.form-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
  align-items: start;
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
}

/* =======================================================
   GRUPOS DE FORMULRIO - TAMANHOS UNIFORMES
   ======================================================= */

.form-group {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
}

.form-group label {
  font-weight: 600;
  margin-bottom: 8px;
  color: #333;
  font-size: 0.9rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.form-group input,
.form-group select,
.form-group textarea {
  padding: 12px 15px;
  border: 2px solid #e1e1e1;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: white;
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
  min-width: 0;
  height: 48px; /* ALTURA UNIFORME PARA TODOS OS CAMPOS */
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #2d7a31;
  box-shadow: 0 0 0 3px rgba(30, 90, 35, 0.1);
}

/* Campos de erro */
.form-group input.error,
.form-group select.error {
  border-color: #e74c3c;
}

.error-message {
  color: #e74c3c;
  font-size: 0.8rem;
  margin-top: 5px;
}

/* Campos desabilitados - CORRIGIDO PARA TEMA ESCURO */
.form-group input:disabled,
.form-group select:disabled {
  background-color: #e8e8e8 !important; /*  Fundo mais claro */
  color: #333333 !important;            /*  Texto escuro bem visvel */
  cursor: not-allowed;
  opacity: 1 !important;                /*  Remover opacidade que deixava invisvel */
  font-weight: 600 !important;          /*  Texto mais destacado */
}

/* =======================================================
   UNIDADES CONSUMIDORAS - LAYOUT CORRIGIDO
   ======================================================= */

.ucs-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;
}

/* Cada UC em uma linha separada */
.uc-row {
  background: white;
  border-radius: 10px;
  padding: 20px;
  border: 2px solid #e1e1e1;
  transition: border-color 0.3s ease;
  width: 100%;
  box-sizing: border-box;
}

.uc-row:hover {
  border-color: #2d7a31;
}

/* Header da UC com ttulo e boto remover */
.uc-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  border-bottom: 1px solid #e1e1e1;
  padding-bottom: 10px;
}

.uc-header h3 {
  margin: 0;
  color: #333;
  font-size: 1.1rem;
  display: flex;
  align-items: center;
  gap: 8px;
}

.uc-header h3::before {
  content: "";
}

.btn-remove-uc {
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 6px;
  padding: 6px 12px;
  font-size: 0.8rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-remove-uc:hover {
  background: #c82333;
  transform: translateY(-1px);
}

/* 5 INPUTS EM UMA LINHA - LAYOUT CORRIGIDO COM TAMANHOS UNIFORMES */
.uc-inputs-row {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 12px;
  width: 100%;
  box-sizing: border-box;
}

.uc-inputs-row .form-group {
  margin-bottom: 0;
}

.uc-inputs-row .form-group label {
  font-size: 0.85rem;
  margin-bottom: 6px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.uc-inputs-row .form-group input,
.uc-inputs-row .form-group select {
  padding: 8px 10px;
  font-size: 0.9rem;
  height: 40px; /* ALTURA UNIFORME PARA OS INPUTS DAS UCs */
}

/* Aes das UCs - BOTO ABAIXO */
.uc-actions {
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid #e1e1e1;
  text-align: center;
}

/* =======================================================
   BENEFCIOS - LAYOUT MELHORADO
   ======================================================= */

.beneficios-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 12px;
  margin-bottom: 25px;
}

.beneficio-item {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  padding: 12px 15px;
  background: white;
  border-radius: 8px;
  border: 1px solid #e1e1e1;
  transition: all 0.3s ease;
}

.beneficio-item:hover {
  background: #f8f9fa;
  border-color: #2d7a31;
}

.beneficio-item input[type="checkbox"] {
  width: 18px;
  height: 18px;
  margin: 0;
  cursor: pointer;
  flex-shrink: 0;
  margin-top: 2px;
}

.beneficio-item label {
  margin: 0;
  cursor: pointer;
  font-weight: 400;
  color: #333;
  line-height: 1.4;
  text-align: left;
  flex: 1;
}

/* Benefcios adicionais */
.beneficios-adicionais {
  border-top: 2px solid #e1e1e1;
  padding-top: 20px;
}

.beneficios-adicionais h3 {
  margin: 0 0 15px 0;
  color: #333;
  font-size: 1.1rem;
}

.beneficio-adicional {
  display: flex;
  gap: 10px;
  margin-bottom: 10px;
  align-items: center;
}

.beneficio-adicional input {
  flex: 1;
  height: 40px;
}

.beneficio-adicional .btn {
  height: 40px;
  width: 40px;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

/* =======================================================
   BOTES - ESTILOS MELHORADOS
   ======================================================= */

.btn {
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  text-decoration: none;
  box-sizing: border-box;
}

.btn-primary {
  background: linear-gradient(135deg, #2d7a31 0%, #45a049 100%);
  color: white !important;
}

.btn-primary:hover:not(:disabled) {
  background: linear-gradient(135deg, #45a049 0%, #3d8b40 100%);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(30, 90, 35, 0.4);
}

.btn-secondary {
  background: linear-gradient(135deg, #6c757d 0%, #5a6268 100%);
  color: white;
}

.btn-secondary:hover:not(:disabled) {
  background: linear-gradient(135deg, #5a6268 0%, #495057 100%);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(108, 117, 125, 0.4);
}

.btn-danger {
  background: linear-gradient(135deg, #dc3545 0%, #c82333 100%);
  color: white;
}

.btn-danger:hover:not(:disabled) {
  background: linear-gradient(135deg, #c82333 0%, #bd2130 100%);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(220, 53, 69, 0.4);
}

.btn-sm {
  padding: 8px 16px;
  font-size: 0.9rem;
}

.btn:disabled {
  background: #bdc3c7 !important;
  cursor: not-allowed;
  transform: none !important;
  box-shadow: none !important;
  opacity: 0.6;
}

/* =======================================================
   AES DO FORMULRIO
   ======================================================= */

.form-actions {
  display: flex;
  gap: 15px;
  justify-content: flex-end;
  padding: 25px 0;
  border-top: 2px solid #e1e1e1;
  margin-top: 30px;
}

/* =======================================================
   RESPONSIVIDADE
   ======================================================= */

@media (max-width: 1200px) {
  .form-grid-uniform {
    grid-template-columns: repeat(2, 1fr) !important;
    gap: 15px;
  }
  
  .uc-inputs-row {
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
  }
}

@media (max-width: 768px) {
  .form-grid-uniform {
    grid-template-columns: 1fr !important;
    gap: 15px;
  }
  
  .uc-inputs-row {
    grid-template-columns: 1fr;
    gap: 10px;
  }
  
  .form-actions {
    flex-direction: column;
    align-items: stretch;
  }
  
  .btn {
    justify-content: center;
  }

  .beneficio-item {
    padding: 10px 12px;
    gap: 10px;
  }

  .beneficio-item input[type="checkbox"] {
    width: 16px;
    height: 16px;
  }

  .container {
    padding: 15px;
  }

  .form-section {
    padding: 20px;
    margin-bottom: 20px;
  }

  .uc-inputs-row .form-group input,
  .uc-inputs-row .form-group select {
    height: 44px;
  }
}

@media (max-width: 600px) {
  .uc-inputs-row .form-group input,
  .uc-inputs-row .form-group select {
    padding: 6px 8px;
    font-size: 0.85rem;
    height: 40px;
  }

  .uc-inputs-row .form-group label {
    font-size: 0.8rem;
  }
  
  .form-group input,
  .form-group select {
    height: 44px;
  }
}

/* =======================================================
   MELHORIAS DE ACESSIBILIDADE
   ======================================================= */

.form-group input:focus,
.form-group select:focus {
  outline: 2px solid #2d7a31;
  outline-offset: 2px;
}

.btn:focus {
  outline: 2px solid #2d7a31;
  outline-offset: 2px;
}

/* =======================================================
   ANIMAES
   ======================================================= */

.uc-row {
  animation: slideUp 0.3s ease-out;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.beneficio-item {
  animation: fadeIn 0.2s ease-in;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

/* Estados de carregamento */
.form-group.loading input,
.form-group.loading select {
  background: #f8f9fa;
  animation: pulse 1.5s ease-in-out infinite;
}

@keyframes pulse {
  0% { opacity: 1; }
  50% { opacity: 0.7; }
  100% { opacity: 1; }
}

/* =======================================================
   MELHORIAS ESPECFICAS PARA UNIFORMIDADE
   ======================================================= */

/* Garantir que todos os selects tenham a mesma aparncia */
.form-group select {
  appearance: none;
  background-image: url("data:image/svg+xml;charset=US-ASCII,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 4 5'><path fill='%23666' d='m0 1 2 2 2-2z'/></svg>");
  background-repeat: no-repeat;
  background-position: right 12px center;
  background-size: 12px;
  padding-right: 40px;
}

/* Ajustes finos para manter uniformidade visual */
.form-grid-uniform .form-group input,
.form-grid-uniform .form-group select {
  min-height: 48px;
  max-height: 48px;
}

/* Especfico para o campo de recorrncia quando desabilitado */
.form-group select:disabled {
  background-color: #e8e8e8 !important; /*  Fundo mais claro */
  color: #333333 !important;            /*  Texto escuro bem visvel */
  cursor: not-allowed !important;
  font-weight: 600 !important;          /*  Texto mais destacado */
}
/* Placeholder styling */
.form-group input::placeholder {
  color: #999;
  opacity: 1;
}

.form-group input:disabled::placeholder {
  color: #666 !important; /*  Placeholder mais escuro e visvel */
}

.form-group-double {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.double-inputs {
  display: flex;
  gap: 8px;
  width: 100%;
}

.input-half {
  display: flex;
  flex-direction: column;
  flex: 1;
  width: 100%;
}

.input-half label {
  font-weight: 600;
  margin-bottom: 8px;
  color: #333;
  font-size: 0.85rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.input-half input {
  padding: 12px 8px;
  border: 2px solid #e1e1e1;
  border-radius: 8px;
  font-size: 0.9rem;
  transition: all 0.3s ease;
  background: white;
  width: 100%;
  box-sizing: border-box;
  height: 48px;
}

.input-half input:focus {
  outline: none;
  border-color: #2d7a31;
  box-shadow: 0 0 0 3px rgba(30, 90, 35, 0.1);
}

.input-half input.error {
  border-color: #e74c3c;
}

/* Responsividade para campos duplos */
@media (max-width: 768px) {
  .double-inputs {
    flex-direction: column;
    gap: 12px;
  }
  
  .input-half label {
    font-size: 0.9rem;
  }
  
  .input-half input {
    height: 44px;
  }
}

/* TEMA ESCURO ESPECFICO PARA NOVA PROPOSTA */
.form-section {
  background: #2d2d2d !important; /*  Nvel 1: Sees */
  border-left: 4px solid #2d7a31 !important;
  color: #f0f0f0 !important;
}

.form-section h2 {
  color: #f0f0f0 !important;
}

/* UCs - seguindo hierarquia */
.uc-row {
  background: #2d2d2d !important;
  border: 1px solid #555 !important;
}

/* Benefcios */
.beneficio-item {
  background: #3d3d3d !important; /*  Nvel 2: Como inputs */
  border: 1px solid #555 !important;
  color: #f0f0f0 !important;
}

.beneficio-item label {
  color: #f0f0f0 !important;
}

/* Benefcios adicionais */
.beneficios-adicionais {
  border-top: 2px solid #444 !important;
}

.beneficios-adicionais h3 {
  color: #f0f0f0 !important;
}

/* Loading overlay */
.loading-overlay {
  background: rgba(26, 26, 26, 0.9) !important;
}

.loading-content {
  background: #2d2d2d !important;
  color: #f0f0f0 !important;
  border: 1px solid #444 !important;
  border-radius: 8px;
  padding: 20px;
}

/* Container do upload de arquivo */
.file-upload-container {
  position: relative;
  display: flex;
  align-items: center;
  gap: 8px;
}

.file-input {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.file-upload-label {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 16px;
  border: 2px dashed #e1e1e1;
  border-radius: 8px;
  background: #fafafa;
  cursor: pointer;
  transition: all 0.3s ease;
  min-height: 48px;
  flex: 1;
  text-align: center;
  font-size: 0.85rem;
}

.file-upload-label:hover {
  border-color: #2d7a31;
  background: #f0f9f0;
}

.file-selected {
  color: #2e7d32;
  font-weight: 500;
}

.file-placeholder {
  color: #666;
}

.btn-remove-file {
  background: #e74c3c;
  color: white;
  border: none;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 14px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}

.file-help-text {
  color: #666;
  font-size: 0.75rem;
  margin-top: 4px;
  font-style: italic;
}

.optional-field {
  color: #999;
  font-weight: normal;
  font-size: 0.75rem;
  margin-left: 4px;
  
}

/* Layout de 6 campos por linha para UCs - VERSO CORRIGIDA */
.uc-inputs-grid {
  display: grid;
  grid-template-columns: 1.5fr 0.8fr 1.2fr 1fr 0.8fr 1.2fr;
  gap: 12px;
  align-items: end; /* Volta para 'end' para alinhar inputs na base */
  margin-top: 15px;
}

.uc-field {
  display: flex;
  flex-direction: column;
}

/* CORREO DA COR DOS LABELS */
.uc-field label {
  font-weight: 600;
  margin-bottom: 6px;
  font-size: 0.8rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  min-height: 20px;
  /* Remove qualquer override de cor - usa a cor padro do tema */
}

/* Garante que labels das UCs usem a mesma cor dos outros labels da pgina */
.form-section .uc-field label,
.uc-row .uc-field label {
  color: inherit; /* Herda a cor do elemento pai */
}

.uc-field input,
.uc-field select {
  padding: 10px 8px;
  border: 2px solid #e1e1e1;
  border-radius: 6px;
  font-size: 0.85rem;
  transition: all 0.3s ease;
  background: white;
  height: 42px;
  box-sizing: border-box;
}

.uc-field input:focus,
.uc-field select:focus {
  outline: none;
  border-color: #2d7a31;
  box-shadow: 0 0 0 3px rgba(30, 90, 35, 0.1);
}

.uc-field input.error,
.uc-field select.error {
  border-color: #e74c3c;
}

.uc-field .error-message {
  color: #e74c3c;
  font-size: 0.7rem;
  margin-top: 4px;
  font-weight: 500;
}

/* Upload de arquivo - VERSO CORRIGIDA */
.uc-field .file-upload-container {
  position: relative;
  display: flex;
  align-items: center;
  gap: 6px;
  height: 42px; /* Mesma altura dos outros inputs */
}

.file-upload-label {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 10px;
  border: 2px dashed #e1e1e1;
  border-radius: 6px;
  background: #fafafa;
  cursor: pointer;
  transition: all 0.3s ease;
  flex: 1;
  text-align: center;
  font-size: 0.75rem;
  height: 100%;
  box-sizing: border-box;
}

.file-upload-label:hover {
  border-color: #2d7a31;
  background: #f0f9f0;
}

/* CORREO DO TEXTO DE AJUDA - fica dentro da clula do grid */
.uc-field .file-help-text {
  color: #666;
  font-size: 0.65rem;
  margin-top: 4px; /* Espao pequeno aps o campo */
  font-style: italic;
  line-height: 1.2;
  /* Remove posicionamento absoluto - fica no fluxo normal */
}

.btn-remove-file {
  background: #e74c3c;
  color: white;
  border: none;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 12px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

/* CORREO: Remove espaamento extra desnecessrio */
.uc-field:last-child {
  margin-bottom: 0;
}

/* Responsividade */
@media (max-width: 1200px) {
  .uc-inputs-grid {
    grid-template-columns: 1fr 1fr 1fr;
    gap: 10px;
  }
}

@media (max-width: 768px) {
  .uc-inputs-grid {
    grid-template-columns: 1fr;
    gap: 12px;
  }
  
  .uc-field label {
    font-size: 0.85rem;
  }
  
  .uc-field input,
  .uc-field select,
  .uc-field .file-upload-container {
    height: 44px;
  }
  
  .uc-field input,
  .uc-field select {
    font-size: 0.9rem;
  }
}

/* Tema escuro - usa as mesmas regras dos outros campos */
body.dark-mode .uc-field input,
body.dark-mode .uc-field select {
  background: #3d3d3d !important;
  border-color: #555 !important;
  color: #f0f0f0 !important;
}

