  background: #3d3d3d !important;
  border-color: #555 !important;
  color: #f0f0f0 !important;
}

body.dark-mode .file-upload-label:hover {
  border-color: #2d7a31 !important;
  background: #4a4a4a !important;
}

body.dark-mode .file-help-text {
  color: #aaa !important;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.spinning-icon {
  animation: spin 1s linear infinite;
}

.btn-primary:disabled {
  cursor: not-allowed !important;
  opacity: 0.7 !important;
}

.btn-secondary:disabled {
  cursor: not-allowed !important;
  opacity: 0.5 !important;
}

/* =======================================================
   BOTES DE AO DA PROPOSTA - CONES GRANDES
   ======================================================= */

.btn-action-proposta {
  /* NO usar all: unset pois isso remove os tamanhos */
  box-sizing: border-box !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  cursor: pointer !important;
  padding: 0 !important;
  margin: 0 !important;
  border: none !important;
  overflow: visible !important;
}

.btn-action-proposta svg {
  /* SVGs do lucide-react com tamanho bem visvel - 40px */
  width: 40px !important;
  height: 40px !important;
  min-width: 40px !important;
  min-height: 40px !important;
  max-width: none !important;
  max-height: none !important;
  display: block !important;
  flex-shrink: 0 !important;
  stroke-width: 2.5 !important; /* Trao um pouco mais grosso para melhor visibilidade */
