/* =======================================================
   MODAL DE TITULARIDADE - Aviso ao abrir pgina
   ======================================================= */

.modal-overlay.modal-titularidade {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  animation: fadeIn 0.3s ease-out;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.modal-titularidade-content {
  background: white;
  border-radius: 16px;
  max-width: 500px;
  width: 90%;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.4);
  animation: slideUp 0.3s ease-out;
  overflow: hidden;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.modal-titularidade-header {
  background: linear-gradient(135deg, #f39c12 0%, #e67e22 100%);
  color: white;
  padding: 20px 25px;
  display: flex;
  align-items: center;
  gap: 12px;
}

.modal-titularidade-header h2 {
  margin: 0;
  font-size: 1.3rem;
  font-weight: 600;
}

.modal-icon-warning {
  font-size: 1.8rem;
}

.modal-titularidade-body {
  padding: 25px;
  background: #fff;
}

.modal-titularidade-text {
  font-size: 1rem;
  line-height: 1.6;
  color: #333;
  margin: 0 0 15px 0;
}

.modal-titularidade-text:last-child {
  margin-bottom: 0;
}

.modal-titularidade-text strong {
  color: #2c3e50;
}

.modal-titularidade-highlight {
  background: #fff3cd;
  border-left: 4px solid #f39c12;
  padding: 12px 15px;
  border-radius: 0 8px 8px 0;
  color: #856404;
  font-weight: 500;
}

.modal-titularidade-footer {
  padding: 15px 25px 20px;
  display: flex;
  justify-content: center;
  background: #f8f9fa;
  border-top: 1px solid #e9ecef;
}

.modal-titularidade-footer .btn-primary,
.modal-titularidade-footer .btn.btn-primary,
.modal-titularidade-footer button.btn-primary {
  background: linear-gradient(135deg, #2d7a31 0%, #1e5b24 100%) !important;
  color: white !important;
  border: none !important;
  padding: 12px 40px !important;
  border-radius: 8px !important;
  font-size: 1rem !important;
  font-weight: 600 !important;
  cursor: pointer;
  transition: all 0.2s ease;
}

.modal-titularidade-footer .btn-primary:hover,
.modal-titularidade-footer .btn.btn-primary:hover,
.modal-titularidade-footer button.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(45, 122, 49, 0.4);
  background: linear-gradient(135deg, #1e5b24 0%, #145a1a 100%) !important;
}
