/* =======================================================
   MODAIS GLOBAIS - DARK MODE
   Sistema unificado de modais para toda a aplicao
   ======================================================= */

/* Overlay do modal */
.common-modal-overlay,
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 20px;
  animation: fadeIn 0.2s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Container do modal */
.common-modal,
.modal {
  background: linear-gradient(135deg, #1e293b 0%, #0f172a 100%);
  border-radius: 16px;
  max-width: 600px;
  width: 100%;
  max-height: 90vh;
  overflow: hidden;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.1);
  display: flex;
  flex-direction: column;
  animation: slideIn 0.3s ease-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* Tamanhos de modal */
.common-modal.small,
.modal.small {
  max-width: 400px;
}

.common-modal.large,
.modal.large {
  max-width: 800px;
}

.common-modal.xlarge,
.modal.xlarge {
  max-width: 1200px;
}

/* Header do modal */
.common-modal-header,
.modal-header {
  padding: 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.03);
  flex-shrink: 0;
}

.common-modal-header h2,
.common-modal-header h3,
.modal-header h2,
.modal-header h3 {
  margin: 0;
  color: white;
  font-size: 1.5rem;
  font-weight: 700;
  display: flex;
  align-items: center;
  gap: 10px;
}

.common-modal-header h2 svg,
.common-modal-header h3 svg,
.modal-header h2 svg,
.modal-header h3 svg {
  color: #3b82f6;
}

/* Boto fechar do modal */
.common-close-btn,
.modal-close-btn {
  background: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 8px;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: rgba(255, 255, 255, 0.9);
  transition: all 0.2s ease;
}

.common-close-btn:hover,
.modal-close-btn:hover {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  transform: scale(1.1);
}

/* Body do modal */
.common-modal-content,
.modal-body {
  padding: 24px;
  overflow-y: auto;
  flex: 1;
  min-height: 0;
}

/* Scroll customizado para o body */
.common-modal-content::-webkit-scrollbar,
.modal-body::-webkit-scrollbar {
  width: 8px;
}

.common-modal-content::-webkit-scrollbar-track,
.modal-body::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 4px;
}

.common-modal-content::-webkit-scrollbar-thumb,
.modal-body::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 4px;
}

.common-modal-content::-webkit-scrollbar-thumb:hover,
.modal-body::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.3);
}

/* Footer do modal */
.common-modal-footer,
.modal-footer {
  padding: 20px 24px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.03);
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 12px;
  flex-shrink: 0;
}

/* Tabs do modal */
.common-modal-tabs,
.modal-tabs {
  display: flex;
  gap: 4px;
  padding: 0 24px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.02);
}

.common-modal-tab,
.modal-tab {
  padding: 14px 20px;
  background: transparent;
  border: none;
  color: rgba(255, 255, 255, 0.6);
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  border-bottom: 3px solid transparent;
  display: flex;
  align-items: center;
  gap: 8px;
}

.common-modal-tab:hover,
.modal-tab:hover {
  color: rgba(255, 255, 255, 0.9);
  background: rgba(255, 255, 255, 0.05);
}

.common-modal-tab.active,
.modal-tab.active {
  color: white;
  border-bottom-color: #3b82f6;
  background: rgba(255, 255, 255, 0.05);
}

/* Modal de loading */
.modal-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px 40px;
  text-align: center;
  gap: 20px;
}

.modal-loading-spinner {
  width: 48px;
  height: 48px;
  border: 4px solid rgba(255, 255, 255, 0.1);
  border-top-color: #3b82f6;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.modal-loading p {
  color: white;
  font-size: 1.1rem;
  margin: 0;
}

/* Modal de confirmao */
.modal-confirmation {
  text-align: center;
  padding: 40px;
}

.modal-confirmation-icon {
  width: 64px;
  height: 64px;
  margin: 0 auto 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background: rgba(239, 68, 68, 0.1);
  color: #ef4444;
}

.modal-confirmation h3 {
  color: white;
  margin: 0 0 12px 0;
  font-size: 1.5rem;
}

.modal-confirmation p {
  color: rgba(255, 255, 255, 0.7);
  margin: 0 0 30px 0;
  font-size: 1rem;
  line-height: 1.6;
}

/* Responsividade */
@media (max-width: 768px) {
  .common-modal-overlay,
  .modal-overlay {
    padding: 10px;
  }

  .common-modal,
  .modal {
    max-width: none;
    width: 100%;
    border-radius: 12px;
  }

  .common-modal-header,
  .modal-header {
    padding: 20px;
  }

  .common-modal-header h2,
  .common-modal-header h3,
  .modal-header h2,
  .modal-header h3 {
    font-size: 1.25rem;
  }

  .common-modal-content,
  .modal-body {
    padding: 20px;
  }

  .common-modal-footer,
  .modal-footer {
    padding: 16px 20px;
    flex-wrap: wrap;
  }

  .common-modal-tabs,
  .modal-tabs {
    overflow-x: auto;
    padding: 0 20px;
  }

  .common-modal-tab,
  .modal-tab {
    padding: 12px 16px;
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .common-modal,
  .modal {
    max-height: 95vh;
  }

  .common-modal-header,
  .modal-header {
    padding: 16px;
  }

  .common-modal-content,
  .modal-body {
    padding: 16px;
  }

  .common-modal-footer,
  .modal-footer {
    padding: 12px 16px;
  }

  .modal-confirmation {
    padding: 30px 20px;
  }
}
