/* RelatoriosPage.css - Dark Theme Modern */

/* ============================================
   CONTAINER PRINCIPAL - TEMA ESCURO
   ============================================ */
.page-container {
  min-height: 100vh;
  background: #1a1a1a !important;
}

.relatorios-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px;
}

/* ============================================
   ACCESS DENIED
   ============================================ */
.access-denied {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 400px;
  padding: 40px;
}

.access-denied-content {
  text-align: center;
  background: linear-gradient(135deg, #1e293b 0%, #0f172a 100%);
  padding: 40px;
  border-radius: 16px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.1);
  max-width: 500px;
}

.access-denied-content h2 {
  color: #ef4444;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  font-size: 1.5rem;
}

.access-denied-content p {
  color: rgba(255, 255, 255, 0.7);
  line-height: 1.6;
  margin-bottom: 12px;
}

.access-denied-content strong {
  color: white;
}

/* ============================================
   FILTROS GLOBAIS - DARK THEME
   ============================================ */
.global-filters {
  background: linear-gradient(135deg, #1e293b 0%, #0f172a 100%);
  border-radius: 16px;
  padding: 24px;
  margin-bottom: 24px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

/* ============================================
   DASHBOARD CONTENT - SEM TABS
   ============================================ */
.dashboard-content {
  position: relative;
  min-height: 400px;
}

.content-wrapper {
  padding: 0;
}

/* ============================================
   LOADING OVERLAY
   ============================================ */
.loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(26, 26, 26, 0.95);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 10;
  border-radius: 16px;
}

.loading-overlay p {
  color: rgba(255, 255, 255, 0.8);
  margin-top: 16px;
  font-size: 1rem;
}

.loading-spinner {
  width: 48px;
  height: 48px;
  border: 4px solid rgba(255, 255, 255, 0.1);
  border-top-color: #3b82f6;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* ============================================
   SEES DO DASHBOARD
   ============================================ */
.dashboard-executivo section {
  margin-bottom: 32px;
}

.dashboard-executivo section h2 {
  color: white;
  font-size: 1.3rem;
  font-weight: 700;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.dashboard-executivo section h2 .inline-icon {
  color: #3b82f6;
}

/* ============================================
   CARDS DE MTRICAS - MODERN DARK
   ============================================ */
.cards-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
  margin-bottom: 32px;
}

.card-metrica {
  background: linear-gradient(135deg, #1e293b 0%, #0f172a 100%);
  border-radius: 16px;
  padding: 24px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.card-metrica::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #3b82f6, #8b5cf6);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.card-metrica:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 35px rgba(0, 0, 0, 0.4);
  border-color: rgba(59, 130, 246, 0.3);
}

.card-metrica:hover::before {
  opacity: 1;
}

.card-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 16px;
}

.card-icon {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(59, 130, 246, 0.1);
  border-radius: 10px;
  color: #3b82f6;
}

.card-title-container h4 {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.875rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin: 0;
}

.card-body {
  margin-top: 8px;
}

.card-value {
  color: white;
  font-size: 2rem;
  font-weight: 700;
  line-height: 1;
}

/* ============================================
   GRFICOS - DARK THEME
   ============================================ */
.graficos-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(450px, 1fr));
  gap: 24px;
  margin-bottom: 32px;
}

.grafico-item {
  background: linear-gradient(135deg, #1e293b 0%, #0f172a 100%);
  border-radius: 16px;
  padding: 24px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

/* ============================================
   RANKING / TABELAS - DARK THEME
   ============================================ */
.ranking-consultores {
  background: linear-gradient(135deg, #1e293b 0%, #0f172a 100%);
  border-radius: 16px;
  padding: 24px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.1);
  margin-bottom: 32px;
}

/* ============================================
   RESUMO DO PERODO
   ============================================ */
.resumo-periodo {
  margin-top: 32px;
}

.resumo-card {
  background: linear-gradient(135deg, #1e293b 0%, #0f172a 100%);
  border-radius: 16px;
  padding: 24px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.resumo-card h3 {
  color: white;
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.resumo-card h3 .inline-icon {
  color: #3b82f6;
}

.resumo-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.resumo-item {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.resumo-item strong {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.875rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.resumo-item span {
  color: white;
  font-size: 1rem;
  font-weight: 600;
}

/* ============================================
   INLINE ICON
   ============================================ */
.inline-icon {
  vertical-align: middle;
  flex-shrink: 0;
}

/* ============================================
   RESPONSIVIDADE
   ============================================ */
@media (max-width: 1200px) {
  .graficos-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  .relatorios-container {
    padding: 12px;
  }

  .cards-grid {
    grid-template-columns: 1fr;
  }

  .graficos-grid {
    grid-template-columns: 1fr;
  }

  .resumo-content {
    grid-template-columns: 1fr;
  }

  .card-value {
    font-size: 1.75rem;
  }

  .dashboard-executivo section h2 {
    font-size: 1.1rem;
  }
}

@media (max-width: 480px) {
  .global-filters,
  .dashboard-content,
  .card-metrica,
  .grafico-item,
  .ranking-consultores,
  .resumo-card {
    border-radius: 12px;
    padding: 16px;
  }

  .card-value {
    font-size: 1.5rem;
  }
}
