/* src/pages/Dashboard.css - Estilos do dashboard */

/* Importar estilos padronizados de indicadores */
@import '../styles/indicators.css';

.page-container {
  min-height: 100vh;
  background: #1a1a1a !important; /*  MUDAR ESTA LINHA */
  background-attachment: fixed;
}

.container {
  margin: 0 auto;
  padding: 20px;
}

/* =======================================================
   SEO DE INFORMAES DO USURIO
   ======================================================= */

.user-info-section {
  margin-bottom: 30px;
}

.user-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 15px;
  padding: 25px;
  display: flex;
  align-items: center;
  gap: 20px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  position: relative;
}

.user-avatar {
  width: 80px;
  height: 80px;
  background: linear-gradient(135deg, #2d7a31, #45a049);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 15px rgba(30, 90, 35, 0.3);
}

.user-icon {
  font-size: 2.5rem;
  filter: drop-shadow(0 2px 4px rgba(255, 255, 255, 0.3));
}

.user-details h3 {
  margin: 0 0 5px 0;
  font-size: 1.8rem;
  font-weight: 700;
  color: #2c3e50;
}

.user-role {
  margin: 0 0 5px 0;
  font-size: 1.1rem;
  font-weight: 600;
  color: #2d7a31;
}

.user-username {
  margin: 0;
  font-size: 0.95rem;
  color: #7f8c8d;
  font-family: 'Courier New', monospace;
}

/* =======================================================
   ESTATSTICAS RPIDAS - ESTILOS ESPECFICOS DO DASHBOARD
   Os estilos base esto em ../styles/indicators.css
   ======================================================= */

/* Tema escuro para labels e valores no Dashboard */
.stat-card .stat-label {
  color: #bbb !important;
}

.stat-card .stat-value {
  color: #2d7a31 !important;
}

/* =======================================================
   AES RPIDAS
   ======================================================= */

.quick-actions {
  margin-bottom: 35px;
}

.quick-actions h2 {
  color: white;
  margin-bottom: 20px;
  font-size: 1.5rem;
  font-weight: 700;
  display: flex;
  align-items: center;
  gap: 10px;
}

.actions-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.action-card {
  background: rgba(255, 255, 255, 0.95);
  border: none;
  border-radius: 15px;
  padding: 25px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: left;
  display: flex;
  flex-direction: column;
  gap: 10px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.action-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.15);
}

.action-card.primary {
  background: linear-gradient(135deg, #2d7a31, #45a049);
  color: white;
}

.action-card.primary .action-label,
.action-card.primary .action-desc {
  color: white;
}

.action-icon {
  font-size: 2rem;
  margin-bottom: 5px;
}

.action-label {
  font-size: 1.2rem;
  font-weight: 700;
  color: #2c3e50;
  margin-bottom: 5px;
}

.action-desc {
  font-size: 0.9rem;
  color: #7f8c8d;
  line-height: 1.4;
}

/* =======================================================
   GERENCIAMENTO DE USURIOS
   ======================================================= */

.user-management {
  background: #2d2d2d;
  border-radius: 15px;
  padding: 30px;
  margin-bottom: 35px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.user-management h2 {
  color: #2c3e50;
  margin-bottom: 25px;
  font-size: 1.5rem;
  font-weight: 700;
  display: flex;
  align-items: center;
  gap: 10px;
}

.management-actions {
  display: flex;
  gap: 15px;
  margin-bottom: 30px;
  flex-wrap: wrap;
}

.create-user-btn {
  background: linear-gradient(135deg, #3498db, #2980b9);
  color: white;
  border: none;
  border-radius: 12px;
  padding: 15px 25px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 10px;
  font-weight: 600;
  font-size: 0.95rem;
  position: relative;
  overflow: hidden;
}

.create-user-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(52, 152, 219, 0.3);
}

.create-user-btn.consultor {
  background: linear-gradient(135deg, #3498db, #2980b9) !important;
  color: white !important;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2) !important;
}

.create-user-btn.consultor:hover {
  background: linear-gradient(135deg, #2980b9, #21618c) !important;
  box-shadow: 0 8px 20px rgba(52, 152, 219, 0.4) !important;
  transform: translateY(-2px) !important;
}

.create-user-btn.consultor .btn-label {
  font-weight: 600 !important;
  color: white !important;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2) !important;
}

.create-user-btn.consultor .btn-icon {
  color: white !important;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2) !important;
}

.create-user-btn.gerente {
  background: linear-gradient(135deg, #e67e22, #d35400);
}

.create-user-btn.gerente:hover {
  box-shadow: 0 8px 20px rgba(230, 126, 34, 0.3);
}

.create-user-btn.vendedor {
  background: linear-gradient(135deg, #3b82f6, #2563eb);
}

.create-user-btn.vendedor:hover {
  box-shadow: 0 8px 20px rgba(59, 130, 246, 0.3);
}

.btn-icon {
  font-size: 1.2rem;
}

.btn-label {
  font-weight: 600;
}

/* =======================================================
   LISTA DA EQUIPE
   ======================================================= */

.team-list h3 {
  color: #2c3e50;
  margin-bottom: 20px;
  font-size: 1.2rem;
  font-weight: 600;
}

.team-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
}

.team-member {
  background: #f8f9fa;
  border-radius: 12px;
  padding: 15px; /* Reduzido de 20px */
  display: flex;
  align-items: center;
  gap: 15px;
  border: 2px solid #e9ecef;
  transition: all 0.3s ease;
  position: relative;
  min-height: 80px; /* Altura controlada */
}

.member-icon-svg {
  flex-shrink: 0;
  color: #2d7a31; /* Cor verde padro */
  display: flex;
  align-items: center;
  justify-content: center;
  transition: color 0.3s ease;
}

/* Cores especficas por role */
.team-member:has(.member-info .member-role:contains("Admin")) .member-icon-svg {
  color: #ffd700; /* Dourado para admin (Crown) */
}

.team-member:hover .member-icon-svg {
  color: #45a049; /* Verde mais escuro no hover */
}

/* Ajustar o card para a nova estrutura */
.team-member {
  background: #f8f9fa;
  border-radius: 12px;
  padding: 15px;
  display: flex;
  align-items: center;
  gap: 15px;
  border: 2px solid #e9ecef;
  transition: all 0.3s ease;
  position: relative;
  min-height: 80px;
}

.team-member:hover {
  border-color: #2d7a31;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.member-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.member-info h4 {
  margin: 0;
  font-size: 1rem;
  font-weight: 600;
  color: #2c3e50;
}

.member-info p {
  margin: 0;
  font-size: 0.85rem;
  color: #6c757d;
}

.member-role {
  font-weight: 500;
  color: #2d7a31 !important;
}

.team-member:hover {
  border-color: #2d7a31;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.member-info h4 {
  margin: 0 0 5px 0;
  font-size: 1.1rem;
  font-weight: 600;
  color: #2c3e50;
}

.member-role {
  margin: 0 0 3px 0;
  font-size: 0.9rem;
  font-weight: 500;
  color: #2d7a31;
}

.member-username {
  margin: 0 0 3px 0;
  font-size: 0.8rem;
  color: #6c757d;
  font-family: 'Courier New', monospace;
}

.member-date {
  margin: 0;
  font-size: 0.75rem;
  color: #adb5bd;
}

/* =======================================================
   MODAL
   ======================================================= */

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal {
  background: white;
  border-radius: 15px;
  max-width: 500px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

.modal-header {
  padding: 25px 30px 20px;
  border-bottom: 1px solid #e9ecef;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-header h3 {
  margin: 0;
  color: #2c3e50;
  font-size: 1.3rem;
  font-weight: 700;
}

.close-btn {
  background: none;
  border: none;
  font-size: 1.2rem;
  cursor: pointer;
  color: #6c757d;
  padding: 5px;
  border-radius: 4px;
  transition: all 0.3s ease;
}

.close-btn:hover {
  background: #f8f9fa;
  color: #495057;
}

.modal-body {
  padding: 25px 30px;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 600;
  color: #2c3e50;
  font-size: 0.95rem;
}

.form-group input {
  width: 100%;
  padding: 12px 15px;
  border: 2px solid #e9ecef;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
  box-sizing: border-box;
}

.form-group input:focus {
  outline: none;
  border-color: #2d7a31;
  box-shadow: 0 0 0 3px rgba(30, 90, 35, 0.1);
}

.modal-footer {
  padding: 20px 30px 25px;
  border-top: 1px solid #e9ecef;
  display: flex;
  gap: 15px;
  justify-content: flex-end;
}

.btn-primary, .btn-secondary {
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  font-size: 0.95rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-primary {
  background: rgba(255, 255, 255, 0.1) !important;
  color: rgba(255, 255, 255, 0.9) !important;
  border: 1px solid rgba(255, 255, 255, 0.2) !important;
}

.btn-primary:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.15) !important;
  transform: translateY(-2px);
  border-color: rgba(255, 255, 255, 0.3) !important;
}

.btn-primary:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.btn-secondary {
  background: #6c757d;
  color: white;
}

.btn-secondary:hover {
  background: #5a6268;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(108, 117, 125, 0.3);
}

/* =======================================================
   RESPONSIVIDADE
   ======================================================= */

@media (max-width: 768px) {
  .container {
    padding: 15px;
  }

  .user-card {
    flex-direction: column;
    text-align: center;
    gap: 15px;
  }

  .user-avatar {
    width: 60px;
    height: 60px;
  }

  .user-icon {
    font-size: 2rem;
  }

  .quick-stats {
    grid-template-columns: repeat(2, 1fr);
    gap: 15px;
  }

  .actions-grid {
    grid-template-columns: 1fr;
    gap: 15px;
  }

  .management-actions {
    flex-direction: column;
  }

  .create-user-btn {
    width: 100%;
    justify-content: center;
  }

  .team-grid {
    grid-template-columns: 1fr;
  }

  .modal {
    margin: 20px;
    width: calc(100% - 40px);
  }

  .modal-header,
  .modal-body,
  .modal-footer {
    padding-left: 20px;
    padding-right: 20px;
  }

  .modal-footer {
    flex-direction: column;
  }

  .btn-primary,
  .btn-secondary {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .quick-stats {
    grid-template-columns: 1fr;
    gap: 12px;
  }

  .user-details h3 {
    font-size: 1.5rem;
  }

  .quick-actions h2,
  .user-management h2 {
    font-size: 1.3rem;
  }

  .action-card {
    padding: 20px;
  }

  .user-management {
    padding: 20px;
  }

  .team-member {
    padding: 15px;
  }

  .member-avatar {
    width: 40px;
    height: 40px;
  }

  .member-icon {
    font-size: 1.2rem;
  }
}

/* ADICIONAR AO FINAL DO src/pages/Dashboard.css - Estilos para modais claros */

/* =======================================================
   MODAL TEMA CLARO - DASHBOARD
   ======================================================= */

