  background: rgba(255, 255, 255, 0.08);
}

.form-group-light input::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.form-group-light select option {
  background: #1e293b;
  color: white;
}

.modal-footer-light {
  padding: 16px 24px 24px 24px;
  display: flex;
  gap: 12px;
  justify-content: flex-end;
}

.btn-primary-light {
