/* src/components/common/Navigation.css - Ajustado para Nova Proposta */

.navigation {
  margin-bottom: 30px;
}

.nav-container {
  background: rgba(45, 45, 45, 0.95); /*  MUDOU: Fundo escuro */
  border-radius: 15px;
  padding: 15px 25px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3); /*  MUDOU: Sombra mais forte */
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1); /*  MUDOU: Borda sutil */
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

.nav-menu {
  display: flex;
  gap: 3px; /* Reduzido de 5px para 3px para acomodar mais itens */
  flex-wrap: wrap;
}

.nav-item {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 10px 16px;
  background: transparent;
  border: 2px solid transparent;
  border-radius: 12px;
  color: #ffffff; /*  MUDOU: Texto branco */
  text-decoration: none;
  font-weight: 600;
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.3s ease;
  white-space: nowrap;
}

.nav-item:hover {
  background: rgba(34, 139, 34, 0.15);
  border-color: rgba(34, 139, 34, 0.3);
  transform: translateY(-2px);
  color: #ffffff;
}

/* =======================================================
   BADGE DE NOTIFICAO
   ======================================================= */

.nav-item {
  position: relative;
}

.nav-badge {
  position: absolute;
  top: -6px;
  right: -6px;
  background: linear-gradient(135deg, #ef4444, #dc2626);
  color: white;
  font-size: 0.65rem;
  font-weight: 700;
  min-width: 18px;
  height: 18px;
  padding: 0 5px;
  border-radius: 9px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 6px rgba(239, 68, 68, 0.4);
  animation: badgePulse 2s ease-in-out infinite;
  z-index: 10;
}

.nav-item.has-badge {
  overflow: visible;
}

@keyframes badgePulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}

.nav-item.active {
  background: linear-gradient(135deg, #2d7a31, #3a9640); /* Verde mais suave e profissional */
  color: white;
  border: 2px solid #236b28;
  box-shadow: 0 2px 6px rgba(45, 122, 49, 0.3),
              inset 0 1px 0 rgba(255, 255, 255, 0.15); /* Sombra mais discreta */
  transform: translateY(-1px); /* Elevao sutil */
  font-weight: 700;
}

.nav-icon {
  font-size: 1.1rem; /* Reduzido de 1.2rem para 1.1rem */
}

.nav-label {
  font-weight: 700;
  letter-spacing: 0.3px; /* Reduzido de 0.5px para 0.3px */
}

/* =======================================================
   REA DO USURIO
   ======================================================= */

.nav-user {
  display: flex;
  align-items: center;
  padding-left: 20px;
  border-left: 2px solid #e9ecef;
}

/* =======================================================
   BOTO DE LOGOUT
   ======================================================= */

.logout-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 10px 16px;
  background: linear-gradient(135deg, #e74c3c, #c0392b);
  color: white;
  border: none;
  border-radius: 10px;
  font-weight: 600;
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.logout-btn svg {
  vertical-align: middle;
  flex-shrink: 0;
}

.logout-btn:hover {
  background: linear-gradient(135deg, #c0392b, #a93226);
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(231, 76, 60, 0.3);
}

.logout-icon {
  font-size: 1.1rem;
}

.logout-label {
  font-weight: 600;
}

/* =======================================================
   RESPONSIVIDADE - AJUSTADA PARA MAIS ITENS
   ======================================================= */

@media (max-width: 1400px) {
  .nav-item {
    padding: 10px 14px; /* Ainda mais compacto */
    font-size: 0.8rem;
  }
  
  .nav-icon {
    font-size: 1rem;
  }
  
  .nav-menu {
    gap: 2px;
  }
}

@media (max-width: 1200px) {
  .nav-container {
    flex-direction: column;
    gap: 15px;
    padding: 20px;
  }

  .nav-user {
    border-left: none;
    border-top: 2px solid #e9ecef;
    padding-left: 0;
    padding-top: 15px;
    width: 100%;
    justify-content: space-between;
  }

  .nav-menu {
    justify-content: center;
    width: 100%;
    gap: 5px; /* Mais espao em layouts empilhados */
  }
  
  .nav-item {
    padding: 10px 16px;
    font-size: 0.85rem;
  }
}

@media (max-width: 768px) {
  .nav-container {
    padding: 15px;
  }

  .nav-menu {
    gap: 4px;
    overflow-x: auto; /* Permite scroll horizontal se necessrio */
    padding-bottom: 5px;
  }

  /* Scroll horizontal personalizado */
  .nav-menu::-webkit-scrollbar {
    height: 4px;
  }

  .nav-menu::-webkit-scrollbar-track {
    background: #f1f1f1;
    border-radius: 2px;
  }

  .nav-menu::-webkit-scrollbar-thumb {
    background: #1e5a23;
    border-radius: 2px;
  }

  .nav-item {
    padding: 8px 12px; /* Mais compacto em mobile */
    font-size: 0.8rem;
    min-width: max-content; /* Evita quebra de linha */
  }

  .nav-label {
    display: none; /* Esconde texto em telas muito pequenas */
  }

  .nav-icon {
    font-size: 1.3rem; /* cone maior quando s o cone aparece */
  }

  /* Badge menor em mobile */
  .nav-badge {
    top: -4px;
    right: -4px;
    min-width: 16px;
    height: 16px;
    font-size: 0.6rem;
    padding: 0 4px;
  }

  .user-details {
    display: none;
  }

  .logout-btn {
    padding: 8px 12px;
    font-size: 0.8rem;
  }

  .logout-label {
    display: none;
  }

  .logout-icon {
    font-size: 1.3rem;
  }
}

@media (max-width: 480px) {
  .nav-container {
    padding: 12px;
  }

  .nav-menu {
    gap: 3px;
    width: 100%;
    justify-content: flex-start; /* Alinha  esquerda para permitir scroll */
  }

  .nav-item {
    padding: 8px 10px;
    flex: 0 0 auto; /* No permite que encolham */
    justify-content: center;
  }

  .nav-icon {
    font-size: 1.2rem;
  }

  .nav-user {
    gap: 10px;
  }

  .logout-btn {
    padding: 6px 10px;
  }

  .logout-icon {
    font-size: 1.2rem;
  }
}

/* =======================================================
   ESTADOS ESPECIAIS
   ======================================================= */

.nav-item:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none !important;
}

.nav-item:disabled:hover {
  background: transparent;
  border-color: transparent;
}

/* Animaes */
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.navigation {
  animation: slideIn 0.3s ease-out;
}

/* Loading state */
.nav-loading {
  background: #f8f9fa;
  border-radius: 15px;
  padding: 20px;
  text-align: center;
  color: #6c757d;
}

.nav-loading::before {
  content: "";
  font-size: 1.5rem;
  margin-right: 10px;
}

/* =======================================================
   MELHORIAS PARA NOVA PROPOSTA
   ======================================================= */

/* Destaque para Nova Proposta quando ativa - MESMA COR DAS OUTRAS SEES */
.nav-item[data-id="nova-proposta"].active {
  background: linear-gradient(135deg, #2d7a31, #3a9640); /* Verde mais suave e profissional */
  color: white;
  border: 2px solid #236b28;
  box-shadow: 0 2px 6px rgba(45, 122, 49, 0.3),
              inset 0 1px 0 rgba(255, 255, 255, 0.15); /* Sombra mais discreta */
  transform: translateY(-1px); /* Elevao sutil */
  font-weight: 700;
}

.nav-item[data-id="nova-proposta"]:hover:not(.active) {
  background: rgba(34, 139, 34, 0.15);
  border-color: rgba(34, 139, 34, 0.3);
}

/* Tooltips para mobile quando s mostra cones */
@media (max-width: 768px) {
  .nav-item {
    position: relative;
  }
  
  .nav-item:hover::after {
    content: attr(title);
    position: absolute;
    bottom: -30px;
    left: 50%;
    transform: translateX(-50%);
    background: #333;
    color: white;
    padding: 4px 8px;
    border-radius: 4px;
    font-size: 0.7rem;
    white-space: nowrap;
    z-index: 1000;
    opacity: 0.9;
  }
}