/* src/pages/ProspecPage.css - VERSO LIMPA SEM DUPLICAES */

/* Importar estilos padronizados de indicadores */
@import '../styles/indicators.css';

.page-container {
  min-height: 100vh;
  background: #1a1a1a !important;
  background-attachment: fixed !important;
}

/* =======================================================
   RESPONSIVIDADE GERAL E NOTEBOOKS
   ======================================================= */

.container {
  width: 90%;         /* Desktop padro */
  margin: 0 auto;
  padding: 20px;
}

/* Responsividade especfica para notebooks (1024px - 1366px) */
@media (min-width: 1024px) and (max-width: 1366px) {
  .container {
    width: 95% !important;  /* Expandir para notebooks */
    padding: 15px 20px;     /* Ajustar padding */
  }

  .table th,
  .table td {
    padding: 10px 12px;     /* Otimizar espaamento */
    font-size: 0.9rem;      /* Ajustar fonte */
  }

  .table {
    min-width: 1300px;      /* Garantir espao para todas colunas da prospec */
  }
}

/* =======================================================
   ESTATSTICAS RPIDAS - ESTILOS ESPECFICOS DA PGINA
   Os estilos base esto em ../styles/indicators.css
   ======================================================= */

/* Nenhum override necessrio - usar padro */

/* =======================================================
   SEO DE FILTROS
   ======================================================= */

.filters-section {
  background: white;
  border-radius: 15px;
  padding: 18px;
  margin-bottom: 18px;
  box-shadow: 0 5px 20px rgba(0,0,0,0.1);
}

.filters-container {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.filters-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 12px;
  align-items: end;
  margin-bottom: 12px;
}

.filter-group {
  display: flex;
  flex-direction: column;
}

.filter-group label {
  font-weight: 600;
  margin-bottom: 8px;
  color: #333;
  font-size: 0.9rem;
}

.filter-group input,
.filter-group select {
  padding: 12px 15px;
  border: 2px solid #e1e1e1;
  border-radius: 8px;
  font-size: 0.95rem;
  transition: border-color 0.3s ease;
  background: white;
}

.filter-group input:focus,
.filter-group select:focus {
  outline: none;
  border-color: #2d7a31;
}

.actions-container {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin-top: 0;
  padding-top: 0;
  border-top: none;
}

/* =======================================================
   BOTES
   ======================================================= */

.btn {
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-primary {
  background: linear-gradient(135deg, #2d7a31 0%, #45a049 100%);
  color: white !important;
}

.btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(30, 90, 35, 0.4);
}

.btn-secondary {
  background: linear-gradient(135deg, #6c757d 0%, #5a6268 100%);
  color: white;
}

.btn-secondary:hover {
  background: linear-gradient(135deg, #5a6268 0%, #495057 100%);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(108, 117, 125, 0.4);
}

.btn:disabled {
  background: #bdc3c7 !important;
  cursor: not-allowed;
  transform: none !important;
  box-shadow: none !important;
}

/* =======================================================
   SEO DA TABELA
   ======================================================= */

.table-section {
  background: white;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
  margin-top: 30px;
}

.table-header {
  background: #2d2d2d;
  padding: 20px 25px;
  border-bottom: 2px solid #e1e1e1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.table-header h2 {
  margin: 0;
  color: #f0f0f0 !important;  /*  ALTERAR DE #333 para #f0f0f0 */
  font-size: 1.3rem;
  display: flex;
  align-items: center;
  gap: 10px;
}

.table-count {
  background: #2d7a31;
  color: white;
  padding: 5px 12px;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 500;
  line-height: 1.3;
  text-align: center;
}

/* Container da tabela com scroll */
.table-container {
  overflow-x: auto;
  max-height: 70vh;
  position: relative;
  background: #2d2d2d;
}

/* Tabela com formatao adequada */
.table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.85rem;
  background: white;
}

/* Cabealho da tabela fixo */
.table thead {
  position: sticky;
  top: 0;
  background: #f8f9fa;
  z-index: 10;
}

.table th {
  background: #f8f9fa;
  padding: 15px 12px;
  text-align: left;
  font-weight: 600;
  color: #333;
  border-bottom: 2px solid #e1e1e1;
  font-size: 0.85rem;
  white-space: nowrap;
  min-width: 100px;
  cursor: pointer;
  user-select: none;
  transition: background-color 0.3s ease;
}

.table th:hover {
  background: #e9ecef;
}

/* Clulas da tabela */
.table td {
  padding: 12px;
  border-bottom: 1px solid #f0f0f0;
  font-size: 0.85rem;
  vertical-align: middle;
  min-width: 80px;
}

/* Hover nas linhas */
.table tr:hover {
  background: #f8f9fa;
}

.table tr:nth-child(even) {
  background: #fbfbfb;
}

.table tr:nth-child(even):hover {
  background: #f0f0f0;
}

/* =======================================================
   ESTILOS ESPECFICOS DAS CLULAS
   ======================================================= */

/* Nmero da proposta */
.numero-proposta {
  font-family: 'Courier New', monospace;
  font-weight: 600;
  color: #333;
}

/* Valores numricos */
.valor {
  text-align: right;
  font-weight: 500;
}

/* Datas */
.data {
  font-family: 'Courier New', monospace;
  color: #666;
}

/* Telefones */
.telefone {
  font-family: 'Courier New', monospace;
  color: #666;
}

/* =======================================================
   STATUS BADGES
   ======================================================= */

.status-badge {
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 0.7rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  white-space: nowrap;
}

.status-aguardando {
  background: #fff3cd;
  color: #856404;
  border: 1px solid #ffeaa7;
}

.status-fechada {
  background: #d4edda !important;
  color: #155724 !important;
  border: 1px solid #c3e6cb !important;
}

/* Manter tambm "fechado" para compatibilidade */
.status-fechado {
  background: #d4edda;
  color: #155724;
  border: 1px solid #c3e6cb;
}

.status-cancelada {
  background: #f8f9fa;
  color: #6c757d;
  border: 1px solid #dee2e6;
  text-decoration: line-through;
}

.linha-cancelada {
  text-decoration: line-through;
  opacity: 0.7;
  background-color: #f8f9fa;
}

.linha-cancelada td {
  color: #6c757d;
  text-decoration: line-through;
}

.status-recusada {
  background: #f8d7da;
  color: #721c24;
  border: 1px solid #f5c6cb;
}

/* =======================================================
   AES DA TABELA - MOVIDO PARA CommonModalsPagesDark.css
   Estilos padronizados e unificados entre todas as pginas
   ======================================================= */

