/* src/components/GerarTermoButton.css */

.gerar-termo-container {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-top: 12px;
}

.gerar-termo-container .btn {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  border: none;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  text-decoration: none;
}

.gerar-termo-container .btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.gerar-termo-container .btn-primary {
  background: linear-gradient(135deg, #007bff, #0056b3);
  color: white;
  box-shadow: 0 2px 4px rgba(0, 123, 255, 0.3);
}

.gerar-termo-container .btn-primary:hover:not(:disabled) {
  background: linear-gradient(135deg, #0056b3, #004085);
  box-shadow: 0 4px 8px rgba(0, 123, 255, 0.4);
  transform: translateY(-1px);
}

.gerar-termo-container .btn-warning {
  background: linear-gradient(135deg, #ffc107, #e0a800);
  color: #212529;
  box-shadow: 0 2px 4px rgba(255, 193, 7, 0.3);
}

.gerar-termo-container .btn-warning:hover:not(:disabled) {
  background: linear-gradient(135deg, #e0a800, #d39e00);
  box-shadow: 0 4px 8px rgba(255, 193, 7, 0.4);
  transform: translateY(-1px);
}

.gerar-termo-container .btn-secondary {
  background: linear-gradient(135deg, #6c757d, #5a6268);
  color: white;
  box-shadow: 0 2px 4px rgba(108, 117, 125, 0.3);
}

.gerar-termo-container .btn-secondary:hover:not(:disabled) {
  background: linear-gradient(135deg, #5a6268, #495057);
  box-shadow: 0 4px 8px rgba(108, 117, 125, 0.4);
  transform: translateY(-1px);
}

.gerar-termo-container .btn-regerar-pdf {
  font-size: 13px;
  padding: 6px 12px;
}

.gerar-termo-container .btn-success {
  background: #3b82f6;
  color: white;
  box-shadow: 0 2px 4px rgba(59, 130, 246, 0.3);
}

.gerar-termo-container .btn-success:hover:not(:disabled) {
  background: #2563eb;
  box-shadow: 0 4px 8px rgba(59, 130, 246, 0.4);
  transform: translateY(-1px);
}

.gerar-termo-container .btn-danger {
  background: linear-gradient(135deg, #dc3545, #c82333);
  color: white;
  box-shadow: 0 2px 4px rgba(220, 53, 69, 0.3);
}

.gerar-termo-container .btn-danger:hover:not(:disabled) {
  background: linear-gradient(135deg, #c82333, #bd2130);
  box-shadow: 0 4px 8px rgba(220, 53, 69, 0.4);
  transform: translateY(-1px);
}

.gerar-termo-container .btn-secondary {
  background: linear-gradient(135deg, #6c757d, #545b62);
  color: white;
  box-shadow: 0 2px 4px rgba(108, 117, 125, 0.3);
}

.gerar-termo-container .btn.loading {
  pointer-events: none;
}

.gerar-termo-container .animate-spin {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.status-info {
  padding: 4px 8px;
  background: #f8f9fa;
  border: 1px solid #dee2e6;
  border-radius: 4px;
  font-size: 12px;
}

.campos-faltantes {
  padding: 4px 8px;
  background: #fff5f5 !important;
  border: 1px solid #fed7d7 !important;
  border-radius: 4px;
  font-size: 11px;
  color: #721c24 !important;
}

.campos-faltantes strong {
  color: #721c24 !important;
  font-weight: 700 !important;
}

.campos-faltantes * {
  color: #721c24 !important;
}

.text-success {
  color: #28a745 !important;
}

.text-warning {
  color: #ffc107 !important;
}

.text-danger {
  color: #dc3545 !important;
}

.text-muted {
  color: #6c757d !important;
}

.ml-1 {
  margin-left: 4px;
}

/* =======================================================
   CORREO: CHECKBOXES OPES DE ENVIO VISVEIS
   ======================================================= */

/* Container das opes de envio */
.opcoes-envio {
  background: #f8f9fa !important;
  border: 1px solid #dee2e6 !important;
  border-radius: 10px !important;
  padding: 18px !important;
  margin-top: 12px !important;
}

.opcoes-envio h5 {
  margin: 0 0 4px 0 !important;
  font-size: 15px !important;
  color: #495057 !important;
  font-weight: 600 !important;
  display: flex !important;
  align-items: center !important;
  gap: 6px !important;
}

.opcoes-help {
  margin: 0 0 16px 0 !important;
  font-size: 12px !important;
  color: #6c757d !important;
  font-style: italic !important;
}

/* Container do checkbox customizado */
.checkbox-label-custom {
  display: flex !important;
  align-items: center !important;
  gap: 12px !important;
  cursor: pointer !important;
  font-size: 14px !important;
  margin: 12px 0 !important;
  padding: 12px 16px !important;
  border-radius: 8px !important;
  background: white !important;
  border: 2px solid #e9ecef !important;
  transition: all 0.2s ease !important;
  user-select: none !important;
}

/* Hover no label */
.checkbox-label-custom:hover {
  border-color: #007bff !important;
  background: #f0f8ff !important;
  transform: translateY(-1px) !important;
  box-shadow: 0 2px 4px rgba(0, 123, 255, 0.1) !important;
}

/* Estado checked do label */
.checkbox-label-custom.checked {
  background: #e7f3ff !important;
  border-color: #007bff !important;
  color: #0056b3 !important;
  font-weight: 600 !important;
  box-shadow: 0 2px 6px rgba(0, 123, 255, 0.15) !important;
}

/* Container visual do checkbox */
.checkbox-container-custom {
  position: relative !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
}

/* Input oculto */
.checkbox-input-hidden {
  position: absolute !important;
  opacity: 0 !important;
  width: 0 !important;
  height: 0 !important;
  margin: 0 !important;
  pointer-events: none !important;
}

/* Visual customizado do checkbox */
.checkbox-visual-custom {
  width: 22px !important;
  height: 22px !important;
  border: 2px solid #ced4da !important;
  border-radius: 6px !important;
  background: white !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  cursor: pointer !important;
  transition: all 0.2s ease !important;
  flex-shrink: 0 !important;
}

/* Estado checked do checkbox visual */
.checkbox-visual-custom.checked {
  background: #007bff !important;
  border-color: #007bff !important;
  transform: scale(1.05) !important;
}

/* cone de check */
.checkbox-visual-custom svg {
  color: white !important;
  stroke-width: 3 !important;
}

/* Container do contedo (cone + texto) */
.checkbox-content-custom {
  display: flex !important;
  align-items: center !important;
  gap: 8px !important;
  flex: 1 !important;
}

/* cones do Lucide */
.checkbox-icon-custom {
  color: #6c757d !important;
  transition: color 0.2s ease !important;
}

.checkbox-label-custom.checked .checkbox-icon-custom {
  color: #007bff !important;
}

/* Texto do checkbox */
.checkbox-text-custom {
  color: inherit !important;
  font-weight: inherit !important;
  font-size: 14px !important;
}

/* Estados de validao */
.opcoes-erro {
  display: flex !important;
  align-items: center !important;
  gap: 6px !important;
  padding: 8px 12px !important;
  background: #fff5f5 !important;
  border: 1px solid #fed7d7 !important;
  border-radius: 6px !important;
  color: #e53e3e !important;
  font-size: 12px !important;
  margin: 8px 0 !important;
}

.opcoes-sucesso {
  display: flex !important;
  align-items: center !important;
  gap: 6px !important;
  padding: 8px 12px !important;
  background: #f0fff4 !important;
  border: 1px solid #c6f6d5 !important;
  border-radius: 6px !important;
  color: #38a169 !important;
  font-size: 12px !important;
  margin: 8px 0 !important;
}

/* Boto desabilitado customizado */
.gerar-termo-container .btn.btn-disabled {
  background: #e9ecef !important;
  color: #6c757d !important;
  border-color: #e9ecef !important;
  cursor: not-allowed !important;
  transform: none !important;
  box-shadow: none !important;
}

.gerar-termo-container .btn.btn-disabled:hover {
  background: #e9ecef !important;
  color: #6c757d !important;
  transform: none !important;
  box-shadow: none !important;
}

/* Responsividade */
@media (max-width: 768px) {
  .opcoes-envio {
    padding: 14px !important;
  }
  
  .checkbox-label-custom {
    padding: 10px 14px !important;
    font-size: 13px !important;
    gap: 10px !important;
  }
  
  .checkbox-visual-custom {
    width: 20px !important;
    height: 20px !important;
  }
  
  .checkbox-visual-custom svg {
    width: 10px !important;
    height: 10px !important;
  }
  
  .checkbox-icon-custom {
    width: 14px !important;
    height: 14px !important;
  }
  
  .checkbox-text-custom {
    font-size: 13px !important;
  }
  
  .opcoes-erro,
  .opcoes-sucesso {
    font-size: 11px !important;
    padding: 6px 10px !important;
  }
}

/* Animaes */
.checkbox-visual-custom svg {
  animation: checkmark-appear 0.2s ease-in-out;
}

@keyframes checkmark-appear {
  from {
    opacity: 0;
    transform: scale(0.3);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* Efeitos de focus para acessibilidade */
.checkbox-label-custom:focus-within .checkbox-visual-custom {
  box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25) !important;
}

/* Garantir que mltiplas selees funcionem */
.checkbox-label-custom input[type="checkbox"] {
  pointer-events: auto !important;
}

