   ============================================================================ */

/* Status Badge para documento rejeitado */
.gerar-termo-container .status-badge.rejeitado {
  background: linear-gradient(135deg, #dc3545, #c82333);
  color: white;
  padding: 4px 12px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  box-shadow: 0 2px 4px rgba(220, 53, 69, 0.3);
}

/* Container de informaes sobre rejeio */
.gerar-termo-container .rejection-info {
  margin-top: 12px;
  padding: 12px 16px;
  background: #f8d7da;
  border: 1px solid #f5c6cb;
  border-left: 4px solid #dc3545;
  border-radius: 6px;
  color: #721c24;
}

.gerar-termo-container .rejection-info small {
  font-weight: 500;
  line-height: 1.4;
}

/* Container do motivo da rejeio */
.gerar-termo-container .rejection-reason {
  margin-top: 12px;
  padding: 12px;
  background: #fff3cd;
  border: 1px solid #ffeaa7;
  border-radius: 6px;
}

.gerar-termo-container .rejection-reason p {
  margin: 0 0 8px 0;
  font-weight: 600;
  color: #856404;
}

.gerar-termo-container .reason-text {
  background: white;
  padding: 10px 12px;
  border: 1px solid #e9ecef;
  border-radius: 4px;
  color: #495057;
  font-style: italic;
  line-height: 1.4;
}

/* Estilo especial para boto de gerar novo termo aps rejeio */
.gerar-termo-container .btn-primary.loading {
  background: linear-gradient(135deg, #6c757d, #495057);
  cursor: not-allowed;
}

/* Animao sutil para destacar a ao disponvel */
@keyframes pulseRejected {
  0% {
    box-shadow: 0 0 0 0 rgba(220, 53, 69, 0.3);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(220, 53, 69, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(220, 53, 69, 0);
  }
}

.gerar-termo-container .status-badge.rejeitado {
  animation: pulseRejected 2s infinite;
}

/* Responsividade para seo de documento rejeitado */
@media (max-width: 768px) {
  .gerar-termo-container .rejection-info {
    padding: 10px 12px;
    margin-top: 10px;
  }
  
  .gerar-termo-container .rejection-reason {
    padding: 10px;
    margin-top: 10px;
  }
  
  .gerar-termo-container .reason-text {
    padding: 8px 10px;
    font-size: 14px;
  }
}

/* Estado hover para boto de gerar novo termo */
.gerar-termo-container .btn-primary:not(:disabled):not(.loading):hover {
  background: linear-gradient(135deg, #0056b3, #004085);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(0, 123, 255, 0.3);
  transition: all 0.2s ease;
}

/*  ESTILOS PARA UPLOAD MANUAL - ADICIONAR ao arquivo GerarTermoButton.css */

/* Seo PDF gerado com duas opes */
.acoes-pdf-gerado {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.btn-visualizar-pdf {
  align-self: flex-start;
}

.opcoes-pos-pdf {
  background: #f8f9fa;
  border: 1px solid #dee2e6;
  border-radius: 8px;
  padding: 16px;
  margin-top: 12px;
}

.opcoes-pos-pdf h5 {
  margin: 0 0 8px 0;
  color: #495057;
  font-size: 16px;
  font-weight: 600;
}

.opcoes-help {
  margin: 0 0 16px 0;
  color: #6c757d;
  font-size: 14px;
}

.opcoes-envio-manual {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
  margin-bottom: 16px;
}

.btn-autentique {
  background: #3b82f6;
  border: none;
  color: white;
  padding: 12px 16px;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  font-weight: 500;
  transition: all 0.2s ease;
}

.btn-autentique:hover:not(:disabled) {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.4);
}

.btn-upload-manual {
  background: linear-gradient(135deg, #007bff, #0056b3);
  border: none;
  color: white;
  padding: 12px 16px;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  font-weight: 500;
  transition: all 0.2s ease;
}

.btn-upload-manual:hover:not(:disabled) {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(0, 123, 255, 0.3);
}

/* Container do upload manual */
.upload-manual-container {
  background: #e3f2fd;
  border: 1px solid #2196f3;
  border-radius: 8px;
  padding: 20px;
  margin-top: 16px;
  animation: slideDown 0.3s ease-out;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.upload-manual-container h5 {
  margin: 0 0 8px 0;
  color: #1976d2;
  font-size: 16px;
  font-weight: 600;
}

.upload-help {
  margin: 0 0 16px 0;
  color: #424242;
  font-size: 14px;
  line-height: 1.5;
}

.upload-area {
  margin-bottom: 16px;
}

.upload-input {
  width: 100%;
  padding: 12px;
  border: 2px dashed #2196f3;
  border-radius: 6px;
  background: white;
  color: #424242;
  font-size: 14px;
  transition: all 0.2s ease;
  cursor: pointer;
}

.upload-input:hover {
  border-color: #1976d2;
  background: #f5f5f5;
}

.upload-input:focus {
  outline: none;
  border-color: #1976d2;
  box-shadow: 0 0 0 2px rgba(33, 150, 243, 0.2);
}

.arquivo-selecionado {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: white;
  border: 1px solid #2196f3;
  border-radius: 4px;
  padding: 12px 16px;
  margin-top: 8px;
  animation: fadeIn 0.2s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.arquivo-info {
  color: #424242;
  font-size: 14px;
  font-weight: 500;
}

.btn-remover-arquivo {
  background: none;
  border: none;
  color: #f44336;
  cursor: pointer;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 16px;
  font-weight: bold;
  transition: background-color 0.2s ease;
}

.btn-remover-arquivo:hover {
  background: rgba(244, 67, 54, 0.1);
}

.upload-actions {
  display: flex;
  gap: 12px;
  justify-content: flex-end;
}

.upload-actions .btn {
  min-width: 120px;
  padding: 10px 16px;
}

/* Responsividade */
@media (max-width: 768px) {
  .opcoes-envio-manual {
    grid-template-columns: 1fr;
    gap: 8px;
  }
  
  .btn-autentique,
  .btn-upload-manual {
    padding: 10px 12px;
    font-size: 14px;
  }
  
  .upload-manual-container {
    padding: 16px;
  }
  
  .upload-actions {
    flex-direction: column;
  }
  
  .upload-actions .btn {
    min-width: auto;
    width: 100%;
  }
}

@media (max-width: 480px) {
  .opcoes-pos-pdf {
    padding: 12px;
  }
  
  .opcoes-envio-manual {
    gap: 6px;
  }
  
  .btn-autentique,
  .btn-upload-manual {
    padding: 8px 10px;
    font-size: 13px;
  }
}
