/* ProspecModalDark.css - Estilos dark theme para modal de edio da ProspecPage */

/* ===============================================
   SEES DO MODAL - TEMA DARK
   =============================================== */

.secao-modal {
  background: rgba(255, 255, 255, 0.05) !important;
  border-radius: 12px !important;
  padding: 20px !important;
  margin-bottom: 20px !important;
  border-left: 4px solid #3b82f6 !important;
  border: 1px solid rgba(255, 255, 255, 0.1) !important;
}

.titulo-secao {
  margin: 0 0 16px 0 !important;
  color: white !important;
  font-size: 1.1rem !important;
  font-weight: 700 !important;
  display: flex !important;
  align-items: center !important;
  gap: 8px !important;
  padding-bottom: 12px !important;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1) !important;
}

/* ===============================================
   CAMPOS DE FORMULRIO - TEMA DARK
   =============================================== */

.common-modal .form-group label {
  color: rgba(255, 255, 255, 0.9) !important;
  font-size: 0.9rem !important;
  font-weight: 500 !important;
  margin-bottom: 8px !important;
}

.common-modal .form-group input,
.common-modal .form-group select,
.common-modal .form-group textarea {
  background: rgba(255, 255, 255, 0.05) !important;
  border: 1px solid rgba(255, 255, 255, 0.1) !important;
  border-radius: 8px !important;
  color: white !important;
  padding: 12px 16px !important;
  font-size: 0.95rem !important;
  transition: all 0.3s ease !important;
}

.common-modal .form-group input:focus,
.common-modal .form-group select:focus,
.common-modal .form-group textarea:focus {
  outline: none !important;
  border-color: #3b82f6 !important;
  background: rgba(255, 255, 255, 0.08) !important;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1) !important;
}

.common-modal .form-group input:disabled,
.common-modal .form-group select:disabled,
.common-modal .form-group textarea:disabled,
.common-modal .input-readonly {
  opacity: 0.6 !important;
  cursor: not-allowed !important;
  background: rgba(255, 255, 255, 0.02) !important;
}

/* Select options com fundo dark */
.common-modal .form-group select option {
  background: #1e293b !important;
  color: white !important;
}

/* ===============================================
   SELETOR DE TIPO DE DOCUMENTO - DARK
   =============================================== */

.common-modal .document-type-selector {
  display: flex !important;
  gap: 16px !important;
  margin-bottom: 16px !important;
  padding: 16px !important;
  background: rgba(255, 255, 255, 0.03) !important;
  border-radius: 8px !important;
  border: 1px solid rgba(255, 255, 255, 0.1) !important;
  justify-content: center !important;
}

.common-modal .radio-option {
  display: flex !important;
  align-items: center !important;
  gap: 8px !important;
  cursor: pointer !important;
  padding: 8px 16px !important;
  border-radius: 6px !important;
  transition: all 0.2s ease !important;
  background: rgba(255, 255, 255, 0.05) !important;
  border: 2px solid transparent !important;
}

.common-modal .radio-option:hover {
  background: rgba(255, 255, 255, 0.08) !important;
}

.common-modal .radio-option input[type="radio"]:checked + span {
  font-weight: 600 !important;
  color: #60a5fa !important;
}

.common-modal .radio-option:has(input[type="radio"]:checked) {
  background: rgba(59, 130, 246, 0.2) !important;
  border-color: #3b82f6 !important;
}

.common-modal .radio-option span {
  font-weight: 500 !important;
  color: rgba(255, 255, 255, 0.9) !important;
  font-size: 0.95rem !important;
}

/* ===============================================
   CAMPOS DE DOCUMENTO - DARK
   =============================================== */

.common-modal .document-fields,
.common-modal .common-fields {
  background: rgba(255, 255, 255, 0.03) !important;
  padding: 16px !important;
  border-radius: 8px !important;
  border: 1px solid rgba(255, 255, 255, 0.1) !important;
  margin-top: 12px !important;
}

/* ===============================================
   CAMPOS DE ARQUIVO - DARK
   =============================================== */

.common-modal .file-group input[type="file"] {
  padding: 12px !important;
  border: 2px dashed rgba(59, 130, 246, 0.5) !important;
  background: rgba(255, 255, 255, 0.05) !important;
  cursor: pointer !important;
  border-radius: 8px !important;
  transition: all 0.2s ease !important;
  font-size: 0.9rem !important;
  color: rgba(255, 255, 255, 0.8) !important;
}

.common-modal .file-group input[type="file"]:hover {
  border-color: #3b82f6 !important;
  background: rgba(255, 255, 255, 0.08) !important;
}

.common-modal .file-info {
  color: #4ade80 !important;
  font-size: 0.85rem !important;
  font-weight: 500 !important;
  display: block !important;
  margin-top: 8px !important;
  padding: 8px 12px !important;
  background: rgba(34, 197, 94, 0.1) !important;
  border-radius: 6px !important;
  border-left: 3px solid #4ade80 !important;
}

/* ===============================================
   CONTAINER DE ARQUIVO COM BOTO - DARK
   =============================================== */

.common-modal .file-with-download {
  display: grid !important;
  grid-template-columns: auto 1fr !important;
  gap: 12px !important;
  align-items: start !important;
  width: 100% !important;
}

.common-modal .btn-download-termo {
  background: linear-gradient(135deg, #3b82f6, #2563eb) !important;
  color: white !important;
  border: none !important;
  padding: 12px 16px !important;
  border-radius: 8px !important;
  font-size: 0.85rem !important;
  font-weight: 600 !important;
  cursor: pointer !important;
  transition: all 0.2s ease !important;
  white-space: nowrap !important;
  display: flex !important;
  align-items: center !important;
  gap: 8px !important;
  height: 48px !important;
}

.common-modal .btn-download-termo:hover {
  transform: translateY(-2px) !important;
  box-shadow: 0 8px 20px rgba(59, 130, 246, 0.4) !important;
}

/* ===============================================
   ARQUIVO EXISTENTE - DARK & EXPANDIDO
   =============================================== */

.common-modal .arquivo-existente {
  display: flex !important;
  align-items: center !important;
  justify-content: space-between !important;
  width: 100% !important;
  max-width: 100% !important;
  padding: 12px 16px !important;
  background: rgba(16, 185, 129, 0.1) !important;
  border: 1px solid rgba(16, 185, 129, 0.3) !important;
  border-radius: 8px !important;
  margin-bottom: 8px !important;
  min-height: 48px !important;
}

.common-modal .arquivo-info {
  flex: 1 !important;
  font-size: 0.9rem !important;
  color: #4ade80 !important;
  font-weight: 500 !important;
  white-space: nowrap !important;
  overflow: hidden !important;
  text-overflow: ellipsis !important;
  max-width: none !important;
  margin-right: 12px !important;
}

.common-modal .arquivo-acoes {
  display: flex !important;
  gap: 8px !important;
  flex-shrink: 0 !important;
}

.common-modal .btn-visualizar-doc,
.common-modal .btn-baixar-doc {
  width: 36px !important;
  height: 36px !important;
  padding: 0 !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  border: none !important;
  border-radius: 6px !important;
  cursor: pointer !important;
  transition: all 0.2s ease !important;
  background: rgba(59, 130, 246, 0.2) !important;
  color: #60a5fa !important;
}

.common-modal .btn-visualizar-doc:hover,
.common-modal .btn-baixar-doc:hover {
  background: rgba(59, 130, 246, 0.3) !important;
  transform: scale(1.05) !important;
}

.common-modal .sem-arquivo {
  width: 100% !important;
  padding: 12px 16px !important;
  background: rgba(255, 255, 255, 0.03) !important;
  border: 1px dashed rgba(255, 255, 255, 0.2) !important;
  border-radius: 8px !important;
  color: rgba(255, 255, 255, 0.6) !important;
  font-size: 0.85rem !important;
  font-style: italic !important;
  min-height: 48px !important;
  display: flex !important;
  align-items: center !important;
}

/* ===============================================
   CHECKBOX - DARK
   =============================================== */

.common-modal .checkbox-group-modal {
  padding: 8px 0 !important;
}

.common-modal .checkbox-label {
  display: flex !important;
  align-items: center !important;
  gap: 8px !important;
  cursor: pointer !important;
  font-weight: 500 !important;
  color: rgba(255, 255, 255, 0.9) !important;
  padding: 10px 16px !important;
  background: rgba(255, 255, 255, 0.05) !important;
  border-radius: 8px !important;
  border: 1px solid rgba(255, 255, 255, 0.1) !important;
  transition: all 0.2s ease !important;
  font-size: 0.9rem !important;
}

.common-modal .checkbox-label:hover {
  background: rgba(255, 255, 255, 0.08) !important;
  border-color: rgba(59, 130, 246, 0.5) !important;
}

.common-modal .checkbox-label input[type="checkbox"] {
  width: 16px !important;
  height: 16px !important;
  margin: 0 !important;
  accent-color: #3b82f6 !important;
}

/* ===============================================
   INFORMAO DE STATUS - DARK
   =============================================== */

.common-modal .status-help small {
  color: rgba(255, 255, 255, 0.7) !important;
  font-size: 0.85rem !important;
  line-height: 1.4 !important;
  padding: 10px 16px !important;
  background: rgba(251, 191, 36, 0.1) !important;
  border-radius: 6px !important;
  border-left: 3px solid #fbbf24 !important;
  display: block !important;
}

.common-modal .proposta-info {
  background: rgba(255, 255, 255, 0.05) !important;
  border-radius: 8px !important;
  padding: 16px !important;
  margin-bottom: 20px !important;
  border: 1px solid rgba(255, 255, 255, 0.1) !important;
}

.common-modal .proposta-info p {
  color: rgba(255, 255, 255, 0.8) !important;
  margin: 8px 0 !important;
  font-size: 0.9rem !important;
}

.common-modal .proposta-info strong {
  color: rgba(255, 255, 255, 0.95) !important;
  font-weight: 600 !important;
}

/* ===============================================
   BADGES DE STATUS NO MODAL - DARK
   =============================================== */

.common-modal .status-badge {
  padding: 4px 12px !important;
  border-radius: 20px !important;
  font-size: 0.8rem !important;
  font-weight: 700 !important;
  text-transform: uppercase !important;
  letter-spacing: 0.5px !important;
  width: fit-content !important;
}

.common-modal .status-aguardando {
  background: rgba(251, 191, 36, 0.2) !important;
  color: #fbbf24 !important;
  border: 1px solid rgba(251, 191, 36, 0.3) !important;
}

.common-modal .status-fechado,
.common-modal .status-finalizado {
  background: rgba(34, 197, 94, 0.2) !important;
  color: #4ade80 !important;
  border: 1px solid rgba(34, 197, 94, 0.3) !important;
}

/* ===============================================
   RESPONSIVIDADE
   =============================================== */

@media (max-width: 768px) {
  .secao-modal {
    padding: 16px !important;
    margin-bottom: 16px !important;
  }

  .titulo-secao {
    font-size: 1rem !important;
  }

  .common-modal .file-with-download {
    grid-template-columns: 1fr !important;
  }

  .common-modal .btn-download-termo {
    width: 100% !important;
    justify-content: center !important;
    height: 44px !important;
  }

  .common-modal .document-type-selector {
    flex-direction: column !important;
    gap: 12px !important;
  }

  .common-modal .arquivo-existente {
    padding: 10px 12px !important;
    min-height: 44px !important;
  }

  .common-modal .arquivo-info {
    font-size: 0.85rem !important;
  }

  .common-modal .btn-visualizar-doc,
  .common-modal .btn-baixar-doc {
    width: 32px !important;
    height: 32px !important;
  }
}

@media (max-width: 480px) {
  .secao-modal {
    padding: 12px !important;
  }

  .titulo-secao {
    font-size: 0.95rem !important;
  }

  .common-modal .arquivo-existente {
    padding: 8px 10px !important;
    min-height: 40px !important;
  }

  .common-modal .arquivo-info {
    font-size: 0.8rem !important;
  }

  .common-modal .btn-visualizar-doc,
  .common-modal .btn-baixar-doc {
    width: 28px !important;
    height: 28px !important;
  }

  .common-modal .btn-download-termo {
    height: 40px !important;
    padding: 8px 12px !important;
    font-size: 0.8rem !important;
  }
}

/* ===============================================
   QUADRO DA AUTENTIQUE E STATUS - DARK
   =============================================== */

.common-modal .autentique-status-box,
.common-modal .status-autentique,
.common-modal .documento-status {
  background: rgba(255, 255, 255, 0.05) !important;
  border: 1px solid rgba(255, 255, 255, 0.1) !important;
  border-radius: 8px !important;
  padding: 16px !important;
  margin: 12px 0 !important;
}

.common-modal .autentique-status-box p,
.common-modal .status-autentique p,
.common-modal .documento-status p {
  color: rgba(255, 255, 255, 0.8) !important;
