}

.common-modal .autentique-status-box strong,
.common-modal .status-autentique strong,
.common-modal .documento-status strong {
  color: white !important;
  font-weight: 600 !important;
}

.common-modal .aguardando-assinatura,
.common-modal .status-aguardando-assinatura {
  background: rgba(251, 191, 36, 0.15) !important;
  color: #fbbf24 !important;
  border: 1px solid rgba(251, 191, 36, 0.3) !important;
  padding: 12px 16px !important;
  border-radius: 8px !important;
  font-weight: 600 !important;
  display: flex !important;
  align-items: center !important;
  gap: 8px !important;
}

.common-modal .documento-assinado,
.common-modal .status-assinado {
  background: rgba(34, 197, 94, 0.15) !important;
  color: #4ade80 !important;
  border: 1px solid rgba(34, 197, 94, 0.3) !important;
  padding: 12px 16px !important;
  border-radius: 8px !important;
  font-weight: 600 !important;
  display: flex !important;
  align-items: center !important;
  gap: 8px !important;
}

/* ===============================================
   BOTO ADICIONAR DOCUMENTO EXTRA - DARK
   =============================================== */

.common-modal .btn-add-doc-extra {
  background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%) !important;
  color: white !important;
  border: none !important;
  padding: 8px 16px !important;
  border-radius: 6px !important;
  cursor: pointer !important;
  font-weight: 600 !important;
  font-size: 0.85rem !important;
  transition: all 0.2s ease !important;
  display: flex !important;
  align-items: center !important;
  gap: 6px !important;
}

.common-modal .btn-add-doc-extra:hover {
  transform: translateY(-2px) !important;
  box-shadow: 0 6px 16px rgba(59, 130, 246, 0.4) !important;
}

/* ===============================================
   DOCUMENTOS EXTRAS - CONTAINER DARK
   =============================================== */

.common-modal .documento-extra-item,
.common-modal .doc-extra-container {
  background: rgba(255, 255, 255, 0.05) !important;
  border: 1px solid rgba(255, 255, 255, 0.1) !important;
  border-radius: 8px !important;
  padding: 16px !important;
  margin-bottom: 12px !important;
}

.common-modal .btn-remover-doc-extra {
  background: rgba(239, 68, 68, 0.2) !important;
