.common-modal div[style*="background"][style*="white"],
.common-modal div[style*="backgroundColor"][style*="#fff"],
.common-modal div[style*="backgroundColor"][style*="white"] {
  background-color: rgba(255, 255, 255, 0.05) !important;
}

/* Ensure text visibility */
.common-modal span[style*="color: '#555'"],
.common-modal span[style*="color: '#2e7d32'"] {
  color: rgba(255, 255, 255, 0.8) !important;
}

/* File input inside document extras */
.common-modal input[type="file"][style*="backgroundColor: 'white'"] {
  background-color: rgba(255, 255, 255, 0.05) !important;
  border-color: rgba(255, 255, 255, 0.1) !important;
  color: rgba(255, 255, 255, 0.9) !important;
}


/* ========================================
   DOCUMENTOS EXTRAS - DARK THEME
   ======================================== */

.common-modal .documento-extra-item {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 12px;
  background-color: rgba(255, 255, 255, 0.05) !important;
  border-radius: 6px;
  border: 1px solid rgba(255, 255, 255, 0.1) !important;
}

.common-modal .documento-extra-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 4px;
}

.common-modal .documento-extra-titulo {
  font-size: 13px;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.9) !important;
}


/* ========================================
   CORREO DE CONTRASTE - TEXTOS VISVEIS
   ======================================== */

/* Garantir que todos os labels e spans sejam bem visveis */
.common-modal label {
  color: rgba(255, 255, 255, 0.9) !important;
}

.common-modal span {
  color: rgba(255, 255, 255, 0.9) !important;
}

/* Texto em detalhes e info */
.common-modal .detail-item label,
.common-modal .detail-item span,
.common-modal .details-grid label,
.common-modal .details-grid span {
  color: rgba(255, 255, 255, 0.95) !important;
}

/* Ttulos de sees */
.common-modal h4,
.common-modal .titulo-secao {
  color: rgba(255, 255, 255, 0.95) !important;
  display: flex;
  align-items: center;
  gap: 8px;
}

/* Textos em arquivo info */
.common-modal .arquivo-info {
  color: rgba(255, 255, 255, 0.9) !important;
}

/* Textos em inputs e textareas */
.common-modal input::placeholder,
.common-modal textarea::placeholder,
.common-modal select::placeholder {
  color: rgba(255, 255, 255, 0.4) !important;
}

/* Sem arquivo text */
.common-modal .sem-arquivo {
  color: rgba(255, 255, 255, 0.6) !important;
}

/* Strong tags */
.common-modal strong {
  color: rgba(255, 255, 255, 0.95) !important;
}

/* Paragraphs */
.common-modal p {
  color: rgba(255, 255, 255, 0.85) !important;
}

/* Detail items */
.common-modal .proposta-details label {
  color: rgba(255, 255, 255, 0.7) !important;
  font-weight: 600;
}

.common-modal .proposta-details span:not(.status-badge):not(.arquivo-info) {
  color: rgba(255, 255, 255, 0.95) !important;
}


/* ========================================
   EXCEES PARA GRID 1FR 1FR
   Campos que devem ocupar largura completa
   ======================================== */

/* Status display - ocupar largura completa */
.common-modal .form-row:has(.status-display) {
  grid-template-columns: 1fr !important;
}

/* Documentos extras container - ocupar largura completa */
.common-modal .form-row:has(.documento-extra-item) {
  grid-template-columns: 1fr !important;
}

/* Qualquer form-row com apenas um form-group - ocupar largura completa */
.common-modal .form-row:has(.form-group:only-child) {
  grid-template-columns: 1fr !important;
}

/* Fatura de energia - ocupar largura completa */
.common-modal .form-group:has(input[type="file"][accept*="pdf"]):only-child {
  grid-column: 1 / -1 !important;
}

/* Gerar termo container - j tem regra mas reforar */
.common-modal .form-row:has(.gerar-termo-container) {
  grid-template-columns: 1fr !important;
}

/* Container de documentos extras pai - largura completa */
.common-modal .form-group:has(> div[style*="flexDirection: 'column'"]) {
  grid-column: 1 / -1 !important;
}

/* Documentos extras label - largura completa */
.common-modal .form-group:has(label:contains("Documentos Extras")) {
  grid-column: 1 / -1 !important;
  width: 100% !important;
}

/* Form group com width 100% inline - ocupar tudo */
.common-modal .form-group[style*="width: 100%"],
.common-modal .form-group[style*="width:100%"] {
  grid-column: 1 / -1 !important;
}

/* Form row com width 100% inline - coluna nica */
.common-modal .form-row[style*="width: 100%"],
.common-modal .form-row[style*="width:100%"] {
  grid-template-columns: 1fr !important;
}

